import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useOrder = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1,companyId) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.ORDERS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      // console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const subList = async (orderID,page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      // `${ENDPOINTS.ORDERS}/${ENDPOINTS.DETAILS}/${orderID}/`,
      `${ENDPOINTS.ORDERS}/${ENDPOINTS.DETAIL}/${orderID}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      return [res?.data.suborder];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.ORDERS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Order created successfully!")
    );
    return res?.data;
  };
  const createSub = async (id,payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.ORDERS}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Order created successfully!")
    );
    return res?.data;
  };
  const update = async (payload) => {
    const newPayload ={
      "name":payload.name,
      "metadata":payload.metadata
    }
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.PLANT}/${payload.id}/`,
      newPayload,
      headers,
      (res) => resolveResponse(res, null, "Plant Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.PLANT}/${id}/`,
      headers,
      (res) => resolveResponse(res, null, "Plant deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    subList,
    create,
    createSub,
    update,
    remove
  };
};

export default useOrder;
