import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useUser from "../../store/users";
import Button from "../../components/button";
import AddNewLine from "./AddNewLine";
import AddStation from "./AddStation";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import { Delete } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";

const MachinePage = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [expandmore,setExpandmore] = useState(false)
  const [data, setData] = useState({
    name: "",
    metadata:'',
  });
  const { list, create,update, remove } = useUser();

  useEffect(() => {
    fetchUsers();
  }, [page]);

  const fetchUsers = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1);
    setUsers(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleEdit = async () => { 
    const payload = {...data} 
    setLoading("fetch");
    let id = payload.id
    await update(id,payload)
    setData({});
    setModalOpen(false);
    await fetchUsers();
    setLoading(false);
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchUsers();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchUsers();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 2,
      title: "User Name",
      render: (rowData) => rowData.username,
    },
    {
      id: 3,
      title: "Email",
      render: (rowData) => rowData.email,
    },
    {
      id: 4,
      title: "Full Name",
      render: (rowData) => rowData.fullname,
    },
    {
      id: 4,
      title: "Role",
      render: (rowData) => <Category title={rowData.is_admin?"admin":"Na"}/>,
    },
    {
      id: 5,
      title: "Actions",
      render: (rowData) => (
        <Stack flexDirection={'row'}>
        {!rowData?.is_admin &&(
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleUpdate(rowData?.id,rowData?.is_admin)}
        >
        <AdminPanelSettingsIcon color={rowData.is_admin?"error":"success"} fontSize="small" />
        </IconButton>
        )}
       
        <IconButton
        sx={{ height: 25, width: 25 }}
        onClick={()=>{setModalOpen("edit");setData(rowData)}}
      >
      <EditIcon color="primary" fontSize="small" />
      </IconButton>
      <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton>
      </Stack>
      ),
    },
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Machine Master<span className="fs-30 ml-1 c-gray">({users.length})</span>
        </h1>
        <Button onClick={() => setModalOpen("add")}>Create New Line</Button>
      </div>
        <Typography>
          Company 1 / Plant 1 / SMT
        </Typography>
        <Stack sx={{backgroundColor:COLORS.BLUE_OPAQUE,flexDirection:'row',mt:2,p:0.5,borderRadius:1}}>
        {!expandmore ?(
          <KeyboardArrowRightIcon sx={{cursor:'pointer'}} onClick={()=>setExpandmore(true)}/>
      ):(
      <ExpandMoreIcon sx={{cursor:'pointer'}} onClick={()=>setExpandmore(false)}/>
      )}
      <Typography>Demo</Typography>
      </Stack>
      {expandmore &&(
      <Stack sx={{mx:1}}>
      <Grid sx={{ pt: 1,gap:1 }}  container direction="row">
        <Stack sx={{backgroundColor:COLORS.BG_GRAY,borderRadius:1,width:'fit-content',p:1,px:2}} onClick={()=>{setModalOpen("addstation");setEdit(true)}}>
          <Typography sx={{width:'fit-content'}}>
            Wire Cut
          </Typography>
        </Stack>
        <Stack sx={{backgroundColor:COLORS.BG_GRAY,borderRadius:1,width:'fit-content',p:1,px:2}}>
          <Typography sx={{width:'fit-content'}}>
            CNC
          </Typography>
        </Stack>
        <Stack sx={{width:'fit-content',p:1}}>
         <AddCircleIcon color="primary" onClick={()=>setModalOpen("addstation")}/>
        </Stack>
      </Grid>
      </Stack>
      )}
      <AddNewLine
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        setData={setData}
        onSubmit={handleEdit}
      />
      <AddStation
      edit={edit}
        open={modalOpen === "addstation"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        setData={setData}
        onSubmit={handleEdit}
      />
    </div>
  );
};

export default MachinePage;
