import React from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack } from "@mui/material";
import Button from "../../components/button";

const AddTenant = ({ open, onClose, data, setData, onSubmit, loading }) => {
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">CREATE NEW TENANT</h3>
      <Input
        title="Tenant name"
        placeholder="Enter tenant name"
        width="99%"
        name="name"
        onChange={setData}
        value={data.name}
      />
      {/* <Input
        title="Tenant ID"
        placeholder="Enter tenant id"
        width="99%"
        name="id"
        onChange={setData}
        value={data.id}
      /> */}
      <Input
        title="Secret Key"
        placeholder="Enter secret key"
        width="99%"
        name="secret_key"
        onChange={setData}
        value={data.secret_key}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddTenant;
