import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useCompany from "../../store/company";
import Button from "../../components/button";
import AddCompany from "./AddCompany";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import { Delete ,Checklist} from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import Select from "../../components/input/Select";
import Input from "../../components/input";
import UploadCsv from "../store/UploadCsv";

const ConsumablePage = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata:{}
  });
  const { list, create,update, remove } = useCompany();

  // useEffect(() => {
  //   fetchCompany();
  // }, [page]);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1);
    setUsers(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    console.log("payload",payload)
    setLoading("fetch");
    edit ===true? await update(payload) : await create(payload);
    setData({});
    setModalOpen(false);
    await fetchCompany();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const SEARCH = [
    {
      id: 'name',
      title: "Name"
    },
    {
      id: 'sap_id',
      title: "SAP ID",
    },
    {
      id: 'hsn_code',
      title: "HSN CODE"
    }, 
    {
      id: 'internal_id',
      title: "Internal ID",
    },
    {
      id: 'serialization',
      title: "Serialization",
    },
    {
      id: 'category',
      title: "Category",
    },  
   
  ];
  const COLUMNS = [
    {
      id: 1,
      title: "Name",
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/plantpage',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 2,
      title: "Effective Downtime",
      render: (rowData) => rowData.name
    },
    {
      id: 3,
      title: "Created By",
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/plantpage',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 4,
      title: "Created on",
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/plantpage',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 7,
      title: "Status",
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/plantpage',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 8,
      title: "Lines",
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/plantpage',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 2,
      title: "Threshold",
      render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} onClick={()=> navigate('/plantpage',{state:rowData.id})}>{rowData.name}</Typography>,
    },
    {
      id: 5,
      title: "Action",
      render: (rowData) => (
        <Stack flexDirection={'row'}> 
        <IconButton
        sx={{ height: 25, width: 25 }}
        onClick={()=>{setData(rowData);setEdit(true);setModalOpen("add")}}
      >
      <EditIcon color="primary" fontSize="small" />
      </IconButton>
      </Stack>
      ),
    },
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
         <h1 className="mt-4">
         Consumables<span className="fs-30 ml-1 c-gray">({users.length})</span>
        </h1>
        <Stack sx={{flexDirection:"row",gap:1}}>
        <Button onClick={() => setModalOpen("add")}>Add New Resource</Button>
        {/* <Button onClick={() => setModalOpen("bulkUpload")} bordered >Bulk Upload</Button> */}
        </Stack>
      </div>
      <Stack direction="row" justifyContent="flex-end">
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            // value={searchBy}
            // onChange={(e) => setSearchBy(e.target.value)}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            // onChange={(e)=>{setSearchInput(e.target.value);sList(e.target.value)}}
          />
        </Stack>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddCompany
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
       <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "bulkUpload"}
        // onSubmit={handleSubmitCSV}
        // file={file}
        // setFile={setFile}
        // setCsvData={setCsvData}
        // tab={tab}
      />
    </div>
  );
};

export default ConsumablePage;
