import React, { useEffect, useState } from "react";
import Modal from "../../components/modal";
import {
  SCHEMA_TYPES,
  SERIALIZATION_OPTIONS,
  VALIDATORS,
} from "../../helpers/constants";
import { Step, StepLabel, Stepper } from "@material-ui/core";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import JsonCreator from "../../components/UI/JsonCreator";
import { Validate } from "../../helpers/utils";
import SchemaExtractor from "../../components/UI/SchemaExtractor";

const SCHEMA = [
  {
    title: "Name",
    placeholder: "Enter name",
    type: SCHEMA_TYPES.INPUT,
    name: "name",
    validator: VALIDATORS.REQUIRED,
  },
  {
    title: "HSN Id",
    placeholder: "Enter HSN Id",
    type: SCHEMA_TYPES.INPUT,
    name: "hsn_id",
    // validator: VALIDATORS.REQUIRED,
  },
  {
    title: "Vendor part Id",
    placeholder: "Enter vendor part Id",
    type: SCHEMA_TYPES.INPUT,
    name: "vendor_part_id",
    // validator: VALIDATORS.REQUIRED,
  },
  {
    title: "Cost",
    placeholder: "Enter cost",
    type: SCHEMA_TYPES.INPUT,
    name: "cost",
    validator: VALIDATORS.REQUIRED,
  },
  {
    title: "Expected Life",
    placeholder: "Enter expected life",
    type: SCHEMA_TYPES.INPUT,
    name: "expected_life",
    validator: VALIDATORS.REQUIRED,
  },
  {
    title: "Serialization",
    placeholder: "Select the serialization type",
    type: SCHEMA_TYPES.SELECT,
    name: "serialization",
    validator: VALIDATORS.NUMBER,
    keyValues: SERIALIZATION_OPTIONS,
  },
];

const AddToolMaterialInbound = ({
  open,
  onClose,
  data,
  setData,
  onSubmit,
  isTool,
}) => {
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(new Map());
  const [loading, setLoading] = useState(false);

  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 1) {
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 2) {
      setLoading(true);
      await onSubmit();
      setLoading(false);
      setStep(0);
    }
  };

  const cleanup = () => {
    setErrors(new Map());
    setData({});
    setStep(0);
    onClose();
  };

  const handleBack = () => {
    if (step === 0) {
      cleanup();
    } else {
      setStep((prev) => prev - 1);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  useEffect(() => {
    if (!isTool) cleanup();
  }, [isTool]);

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">CREATE NEW {isTool ? "TOOL" : "MATERIAL"}</h3>
      {/* <Stepper activeStep={step} alternativeLabel>
        {["Info","Specifications", "Location"].map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper> */}
      {step === 0 && (
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={SCHEMA}
        />
      )}
      {step === 1 && (
        <JsonCreator
        setItemss={data?.specification}
          setJson={(value) =>
            setData((prev) => ({ ...prev, specification: value }))
          }
          title="Specification"
        />
      )}
      {step === 2 && (
        <JsonCreator
        setItemss={data?.location}
          setJson={(value) => setData((prev) => ({ ...prev, location: value }))}
          title="Location"
        />
      )}
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddToolMaterialInbound;
