import { Stack, Typography ,Grid,Tab, Tabs } from "@mui/material"
import Dashboard from "./dashboard";
import Select from "../components/input/Select";
import InputDate from "../components/input/indexDate";
import Input from "../components/input/index"
import Table from "../components/table";
import { useState } from "react";
import { COLORS } from "../helpers/constants";

const Dash=()=>{
  const [tab, setTab] = useState(0);
  const [searchBy, setSearchBy] = useState('')
  const [searchInput, setSearchInput] = useState('')
  const [data, setData] = useState({
    "company":'',
    "location":"",
    "division":"",
    "line":""
  })

  const COMPANY = [
    {
      id: 'All',
      title: "All"
    },
    {
      id: 'company list',
      title: "company list",
    }
  ]

  const LOCATION = [
    {
      id: 'All',
      title: "All"
    },
    {
      id: 'location list',
      title: "location list",
    }
  ]

  const DIVISION = [
    {
      id: 'All',
      title: "All"
    },
    {
      id: 'division list',
      title: "division list",
    }
  ]

  const LINE = [
    {
      id: 'All',
      title: "All"
    },
    {
      id: 'line list',
      title: "line list",
    }
  ]

  const COLUMNS = [
    {
      id: 42,
      title: "Division",
      render: (rowData) => rowData.name,
    },
    {
      id: 1,
      title: "Line",
      render: (rowData) => rowData.sap_id,
    },
    {
      id: 2,
      title: "OEE",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 3,
      title: "Availability",
      render: (rowData) =>rowData.serialization_method===1?'No Serialization':rowData.serialization_method===2?"Use Pre-Generated ID":rowData.serialization_method===3?"Use Own ID":"-",
    },
    {
      id: 4,
      title: "Performance",
      render: (rowData) => rowData.vendor_part_id,
    },
    {
      id: 5,
      title: "Quality",
      render: (rowData) => rowData?.category,
    },
    {
      id: 6,
      title: "Total Cycle Time",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 7,
      title: "Total Downtime",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 8,
      title: "Total Available Time",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 9,
      title: "Pass",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 10,
      title: "Fail",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 11,
      title: "Status",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 12,
      title: "Last Event Received At",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 13,
      title: "Details",
      render: (rowData) => rowData.hsn_code,
    }
  ];
  const SEARCH = [
    {
      id: 'Division',
      title: "Division"
    },
    {
      id: 'Line',
      title: "Line",
    },
    {
      id: 'OEE',
      title: "OEE"
    }, 
    {
      id: 'Availability',
      title: "Availability",
    },
    {
      id: 'Performance',
      title: "Performance",
    },
    {
      id: 'Quality',
      title: "Quality",
    }, 
    {
      id: 'Total Cycle Time',
      title: "Total Cycle Time",
    }, 
    {
      id: 'Total Downtime',
      title: "Total Downtime",
    },
    {
      id: 'Total Available Time',
      title: "Total Available Time",
    },
    {
      id: 'Pass',
      title: "Pass",
    },
    {
      id: 'Fail',
      title: "Fail",
    },
    {
      id: 'Status',
      title: "Status",
    },
   
  ];
    return(
        // <Stack>
        //     <Typography variant="h1">
        //         404
        //     </Typography>
        // </Stack>
        <>
        <Stack sx={{p:2,justifyContent:'space-between',flexDirection:'row'}}>
        <Tabs value={tab} onChange={(_e, v) => {setTab(v)}}>
          {["Kanban View", "Table View"].map((label, i) => (
            <Tab label={label} className="fw-500" value={i} />
          ))}
        </Tabs>
        <Stack sx={{flexDirection:'row'}}>
          <Select
            keyValues={COMPANY.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Company"
            classes="mr-1"
            value={data.company}
            onChange={(e) => setData({...data,company:e.target.value})}
          />
          <Select
            keyValues={LOCATION.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Location"
            classes="mr-1"
            value={data.location}
            onChange={(e) =>  setData({...data,location:e.target.value})}
          />
          <Select
            keyValues={DIVISION.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Division"
            classes="mr-1"
            value={data.division}
            onChange={(e) =>  setData({...data,division:e.target.value})}
          />
           <Select
            keyValues={LINE.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Line/Cell"
            classes="mr-1"
            value={data.line}
            onChange={(e) =>  setData({...data,line:e.target.value})}
          />
          <InputDate width={'fit-content'}/>
        </Stack>
        </Stack>
       {tab === 0?(
        <Grid display={'grid'} sx={{gap:1,p:2 }} gridTemplateColumns={{ sm: 'repeat(3, 1fr)', xs: 'repeat(3, 1fr)' }}>
           <Dashboard cCode={COLORS.RED} mState="off"/> 
           <Dashboard cCode={COLORS.YELLOW} mState="downtime"/> 
           <Dashboard cCode={COLORS.GREEN} mState="on"/> 
        </Grid>
       ):(
        <Stack sx={{p:2}}>
        <Stack direction="row" justifyContent="flex-end" sx={{mb:1}}>
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => setSearchBy(e.target.value)}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            value={searchInput}
            onChange={(e)=>{setSearchInput(e.target.value)}}
          />
        </Stack>
        <Table
        // edit={edit}
        columns={COLUMNS}
        // rows={dataList}
        rowsPerPage={5}
        // loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700 text text-nowrap"
        // page={page}
        // setPage={setPage}
        paginator
        serialize
        // total={total}
      />
      </Stack>
      )}
        </>
       
    )
}

export default Dash;