import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Grid, Stack } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import useUserGroup from "../../store/group/user";



const AddTraining = ({ open, onClose, data, setData, onSubmit, loading,edit,users}) => {
  const {list:userGrouplist,groupUser} = useUserGroup()
  const [groups,setGroups] = useState([])

  useEffect(()=>{
    fetchGroups()
  },[])
  const USER_SCHEMA = [
    {
      title: "Training Name",
      placeholder: "Enter Training Name",
      type: SCHEMA_TYPES.INPUT,
      name: "training_name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Training Type",
      placeholder: "Enter Training Type",
      type: SCHEMA_TYPES.INPUT,
      name: "training_type",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Training Description",
      placeholder: "Enter Training Description",
      type: SCHEMA_TYPES.INPUT,
      name: "training_description",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Trainer",
      placeholder: "Enter trainer Name",
      type: SCHEMA_TYPES.INPUT,
      name: "trainer",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Training Admin",
      placeholder: "Enter Training admin",
      type: SCHEMA_TYPES.SELECT,
      name: "training_admin",
      validator: VALIDATORS.REQUIRED,
      keyValues:users,
      multi:true,
      height:'auto'
    },
    {
      title: "Groups",
      placeholder: "Select group",
      type: SCHEMA_TYPES.SELECT,
      name: "group",
      // validator: VALIDATORS.REQUIRED,
      keyValues:groups,
      multi:true,
      height:'auto'
    },
    {
      title: "Trainees",
      placeholder: "Select Trainees",
      type: SCHEMA_TYPES.SELECT,
      name: "trainees",
      // validator: VALIDATORS.REQUIRED,
      keyValues:users,
      multi:true,
      height:'auto'
    },
    {
      title: "Start Date",
      placeholder: "Select Start Date",
      type: SCHEMA_TYPES.DATE,
      name: "start_date",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "End Date",
      placeholder: "Select End Date",
      type: SCHEMA_TYPES.DATE,
      name: "end_date",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Start Time",
      placeholder: "Select start Time",
      type: SCHEMA_TYPES.TIME,
      name: "start_time",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "End Time",
      placeholder: "Select End Time",
      type: SCHEMA_TYPES.TIME,
      name: "end_time",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Test Required at End of Training",
      type: SCHEMA_TYPES.RADIO,
      name: "test_required",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{"value":"yes","label":"Yes"},{"value":"no","label":"No"}],
    },
    {
      title: "Feedback at End of Training",
      type: SCHEMA_TYPES.RADIO,
      name: "feedback_required",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{"value":"yes","label":"Yes"},{"value":"no","label":"No"}],
    },
      data.feedback_required=== 'yes' &&({
      title: "Feedback Link",
      placeholder: "Enter Feedback Link",
      type: SCHEMA_TYPES.INPUT,
      name: "feedback_link",
      validator: VALIDATORS.REQUIRED,
      
    })
  
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    console.log(e.target)
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleNext=async ()=>{
    const _errors = Validate(USER_SCHEMA, data);
        setErrors(_errors);
        if (_errors.size) return;
        await onSubmit()
  }

  const fetchGroups=async ()=>{
    const [res] = await userGrouplist()
    console.log("fetchGroups------>>>>",res)
    const newUsers = res.map(item => ({
      value: item.name,
      key: item.id
    }));
    setGroups(prevUsers => [...prevUsers,...newUsers]);
  }

  return (
  <>
      {/* <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} NEW LINE</h3> */}
      <Grid display={'grid'} gap={1} sx={{}} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}> 
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />   
      </Grid>
      <Stack
        alignItems="flex-end"
        // mt={2}
        mb={1}
      >
        {/* <Button bordered gray onClick={onClose}>
          Cancel
        </Button> */}
        <Button onClick={handleNext} loading={loading}>
          Create
        </Button>
      </Stack>
      </>
  );
};

export default AddTraining;
