import { Stack, Typography, Grid, colors } from "@mui/material"
import OfflineBoltIcon from '@mui/icons-material/OfflineBolt';
import FlashOffIcon from '@mui/icons-material/FlashOff';
import FlashOnIcon from '@mui/icons-material/FlashOn';
import SyncProblemIcon from '@mui/icons-material/SyncProblem';
import SemiRadialBar from "./semi-radial"
import FullRadialBar from "./full-radial"
import { COLORS } from "../helpers/constants";
import Divider from '@mui/material/Divider';
import { useNavigate } from "react-router-dom";

const Dashboard = ({cCode ,mState}) => {
  const navigate = useNavigate()
  return (
    // <Stack sx={{ p: 2 ,width:'100%'}}>
      <Stack sx={{overflow: 'hidden', boxShadow: 3, borderRadius: 2 }}>
        <Stack sx={{ backgroundColor: cCode, p: 1 }}>
          <Stack sx={{ flexDirection: 'row' }}>
            {mState==="on" &&(
              <FlashOnIcon style={{color:COLORS.WHITE}}/>
            )}
             {mState==="off" &&(
              <FlashOffIcon style={{color:COLORS.WHITE}} />
            )}
            {mState==="downtime" &&(
              <SyncProblemIcon style={{color:COLORS.WHITE}} />
            )}
            <Typography sx={{ ml: 1 }}>Line 1</Typography>
          </Stack>
          <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
            <Typography variant="subtitle2" sx={{}}>Running</Typography>
            <Typography variant="subtitle2" sx={{ alignSelf: 'flex-end' }}>Last update: July 29, 2024, 11:34 a.m.</Typography>
          </Stack>
        </Stack>
        <Stack sx={{ mb: 1 }}>
          <SemiRadialBar
            label={"OEE"}
            colorCode={cCode}
            chart={{
              series: [
                { label: 'OEE', value: 37.77 }
              ],
            }}
          />
        </Stack>
        <Divider />
        <Stack sx={{ p: 1 }}>
          <Typography>
            Key Metrics
          </Typography>
          <Stack sx={{ flexDirection: 'row', gap: 1 }}>
            <Stack sx={{ justifyContent: 'center', alignItems: 'center', width: '33%' }}>
              <Typography sx={{ fontWeight: 'bold' }}>Availablity</Typography>
              <FullRadialBar chart={{
                series: [
                  { label: 'America', value: 70 }
                ]
              }} 
              colorCode={cCode}
              />
            </Stack>
            <Stack sx={{ justifyContent: 'center', alignItems: 'center', width: '33%' }}>
              <Typography sx={{ fontWeight: 'bold' }}>Performance</Typography>
              <FullRadialBar chart={{
                series: [
                  { label: 'America', value: 80 }
                ],
              }}
              colorCode={cCode} 
              />
            </Stack>
            <Stack sx={{ justifyContent: 'center', alignItems: 'center', width: '33%' }}>
              <Typography sx={{ fontWeight: 'bold' }}>Quantity</Typography>
              <FullRadialBar chart={{
                series: [
                  { label: 'America', value: 90 }
                ],
              }} 
              colorCode={cCode}
              />
            </Stack>
          </Stack>
        </Stack>
        <Divider />
        <Stack sx={{ p: 1 }}>
          <Typography>
            Product Metrics
          </Typography>
          <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', p: 1 }}>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Total</Typography>
              <Typography>10000</Typography>
            </Stack>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Good</Typography>
              <Typography>10000</Typography>
            </Stack>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>NG</Typography>
              <Typography>23</Typography>
            </Stack>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Pass</Typography>
              <Typography>10000</Typography>
            </Stack>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Fail</Typography>
              <Typography>333</Typography>
            </Stack>
          </Stack>
        </Stack>
        <Divider />
        <Stack sx={{ p: 1 }}>
          <Typography>
            Packet Per Second
          </Typography>
          <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', p: 1 }}>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Best</Typography>
              <Typography>10000</Typography>
            </Stack>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Worst</Typography>
              <Typography>10000</Typography>
            </Stack>
            <Stack>
              <Typography sx={{ fontWeight: 'bold' }}>Average</Typography>
              <Typography>23</Typography>
            </Stack>
          </Stack>
        </Stack>
        <Divider />
        <Typography sx={{ fontWeight: 'bold', textDecoration: 'underline', p: 1,cursor:'pointer' }} onClick={()=>navigate('/operator-portal')}>Operator Portal</Typography>
      </Stack>
    //  </Stack>
  )
}

export default Dashboard;