import { resolveResponse } from "../../api/utils";
import REQUEST, { CONTENT_TYPES } from "../../api";
import { ENDPOINTS } from "../../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "../..";
import { useState, useCallback } from "react";

const useOrder = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const apiRequest = new REQUEST();

  const fetchOrders = useCallback(
    async (pageNumber = 1) => {
      const route = `${ENDPOINTS.ORDERS}/all/?page=${pageNumber}`;
      setLoading(true);
      setError(null);
      try {
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        setError(error.message || "Error in fetchOrders:");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const fetchOrdersMatrixView = useCallback(
    async () => {
      const route = `${ENDPOINTS.ORDERS}/order-matrix-view/`;
      setLoading(true);
      setError(null);
      try {
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        setError(error.message || "Error in fetchOrders:");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const fetchOrdersById = useCallback(
    async (id) => {
      const route = `${ENDPOINTS.ORDERS}/order-details/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in fetching orders by id");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const getOrderHoldQuantity = useCallback(
    async (id) => {
      const route = `${ENDPOINTS.ORDERS}/material-on-hold/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.get(route, {}, headers);
        return response.data;
      } catch (error) {
        console.error(
          error.message || "Error in getting order on hold quantity"
        );
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  const submitBOM = useCallback(
    async (id, body = {}) => {
      const route = `${ENDPOINTS.ORDERS}/bom-submit/${id}/`;
      try {
        setLoading(true);
        const response = await apiRequest.post(route, body, headers);
        return response.data;
      } catch (error) {
        console.error(error.message || "Error in submitting BOM");
      } finally {
        setLoading(false);
      }
    },
    [apiRequest, headers]
  );

  return {
    loading,
    error,
    fetchOrders,
    fetchOrdersMatrixView,
    fetchOrdersById,
    getOrderHoldQuantity,
    submitBOM,
  };
};

export { useOrder };
