import React from "react";
import REQUEST, { CONTENT_TYPES } from "../api";
import { REDUCER_TYPES } from "..";
import { useSelector } from "react-redux";
import { ENDPOINTS } from "../api/endpoints";

const useMaster = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const categories = async () => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.CATEGORIES}/`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data?.results) {
      return res?.data?.results?.map((d) => ({
        key: d?.id,
        value: d?.name,
      }));
    }
    return [];
  };

  const createCategory = async (name) => {
    if (!name) return;
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.CATEGORIES}/`,
      { name },
      headers,
      Function.prototype
    );
    if (res?.data) {
      return res?.data;
    }
  };

  return {
    categories,
    createCategory,
  };
};

export default useMaster;
