import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";

const AddUser = ({ open, onClose, data, setData, onSubmit,handleAdd2, loading ,tab}) => {
 
  const USER_SCHEMA = [
    {
      title: "User Name",
      placeholder: "Enter User Name",
      type: SCHEMA_TYPES.INPUT,
      name: "username",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Email",
      placeholder: "Enter Email",
      type: SCHEMA_TYPES.INPUT,
      name: "email",
      // validator: VALIDATORS.REQUIRED,
    },  
    {
      title: "Full Name",
      placeholder: "Enter Full Name",
      type: SCHEMA_TYPES.INPUT,
      name: "fullname"
    },
   
  ];

  const operatorSchema =[
    {
      title: "Aadhar Number",
      placeholder: "Aadhar Number",
      type: SCHEMA_TYPES.INPUT,
      name: "aadhar_no",
      inputType:"number", 
      validator: VALIDATORS.REQUIRED,
    },
    // {
    //   title: "User Name",
    //   placeholder: "Enter User Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "username",
    // },
     {
      title: "Full Name",
      placeholder: "Enter Full Name",
      type: SCHEMA_TYPES.INPUT,
      name: "fullname"
    }
  ]

  const form2 =[
    {
      title: "Phone Number",
      placeholder: "Phone Number",
      type: SCHEMA_TYPES.INPUT,
      name: "phone_number",
      inputType:"number", 
      // validator: VALIDATORS.REQUIRED, 
    },
    {
      title: "Employee Id",
      placeholder: "employee_id",
      type: SCHEMA_TYPES.INPUT,
      name: "employee_id",
      // validator: VALIDATORS.REQUIRED, 
    },
    tab === "Users"&&({
      title: "Aadhar Number",
      placeholder: "Aadhar Number",
      type: SCHEMA_TYPES.INPUT,
      name: "aadhar_number",
      inputType:"number", 
      // validator: VALIDATORS.REQUIRED, 
    }),
    {
      title: "Employee Type",
      placeholder: "Employee Type",
      type: SCHEMA_TYPES.INPUT,
      name: "employee_type",
      // validator: VALIDATORS.REQUIRED, 
    },
    {
      title: "Date of Joining",
      placeholder: "date_of_joining",
      type: SCHEMA_TYPES.DATE,
      name: "date_of_joining",
      // validator: VALIDATORS.REQUIRED, 
    },
    // {
    //   title: "Supervisor",
    //   placeholder: "supervisor",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "Supervisor",
    //   // validator: VALIDATORS.REQUIRED, 
    // }
  ]

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);

  
  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(tab === "Users"?USER_SCHEMA:operatorSchema, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit()
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 1) {
      const _errors = Validate(form2, data);
      setErrors(_errors);
      if (_errors.size) return;
      await handleAdd2()
      setStep(0);
      return;
    }
   
  };
  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">ADD {tab}</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={step ===0?tab === "Users"?USER_SCHEMA:operatorSchema:form2}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          Next
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddUser;
