import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useTools = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/?page=${page}&page_size=${10}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };
  const searchList = async (page = 1,name,val) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/?search_field=${name}&search_value=${val}&page=${page}&page_size=${10}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };
  const searchOutbound = async (page = 1,name,val) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.OUTBOUNDLIST}/?search_field=${name}&search_value=${val}&page=${page}&page_size=${10}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  const listInbound = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.LIST_INBOUND}/?page=${page}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  const listIssued = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.ALL_ISSUED}/?page=${page}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  const genBarCode = async (payload,slug) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.GENERATEBARCODE}/${slug}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Barcode Generated!")
    );
    if (res?.data) {
      return [res?.data?.serial_number_list];
    }
    return [[], 0];
  };

  const getBarInfo = async (code) => {
    const payload={
      "barcode":code
    }
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.SEARCH}/`,
      payload,
      headers,
      // (res) => resolveResponse(res, null, "Barcode Generated!")
    );
    if (res?.data) {
      return [res?.data];
    }
    return  0;
  };

  const outBoundItem = async (item,internalId) => {
    const payload={
      "serial_number":item.barcode,
      "internal_id":internalId,
      "quantity":item.quantity
    }
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.TOOLS}/${item.id}/${ENDPOINTS.ALL_ISSUED}/`,
      payload,
      headers,
      // (res) => resolveResponse(res, null, "Tool created successfully!")
    );
    return res?.data;
  };
  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.TOOLS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Tool created successfully!")
    );
    return res?.data;
  };

  const createInbound = async (payload,slug) => {
    if (!slug) return;
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.TOOLS}/${slug}/${ENDPOINTS.ALL_INBOUND}/`,
      payload,
      headers,
      Function.prototype
    );
    return res?.data;
  };

  const remove = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.DETAIL}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "Tool deleted successfully!")
    );
    return res?.data;
  };

  const update = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.DETAIL}/${payload.id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Material created successfully!")
    );
    return res?.data;
  };

  const historyList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/${ENDPOINTS.OUTBOUNDLIST}/?page=${page}&page_size=${10}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  return {
    list,
    create,
    remove,
    createInbound,
    listInbound,
    listIssued,
    genBarCode,
    update,
    searchList,
    getBarInfo,
    outBoundItem,
    historyList,
    searchOutbound

  };
};

export default useTools;
