import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
// import Table from "../../components/table";
import PieChart from '../pie'
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import useUser from "../../store/users";
import useMatrix from "../../store/skillmatrix";
import useSkill from "../../store/skill";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Input from "../../components/input";

const SkillMatrix = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const companyId = location.state
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    tranning_type: '',
    metadata: {},
    skill: ''
  });
  const { list, question_list, test_list, assignExam, createTest, update, remove, addQuestion } = useMatrix();
  const { list: userList } = useUser();
  const { list: skill_list } = useSkill()
  const [errors, setErrors] = useState(new Map());
  const [skills, setSkills] = useState([])
  const [testList, setTestList] = useState([])
  const [testId, setTestId] = useState('')




  useEffect(() => {
    fetchSkill()
    fetchTestList()
    //  fetchUsers()
  }, [])

  const fetchTestList = async () => {
    const [_list, _count] = await userList();
    const newOptr = _list.filter(item => item.is_operator);
    const operatorProfiles = await Promise.all(
      newOptr.map(item => list(page + 1, item.id))
    );
    skills.map((item) => {
      operatorProfiles.map((item2) => {
        console.log("newOptr----item2", item2)
        if (item.id === item2.id) {
          console.log("match skill", item.id)
        }
      })

      // let obj = {"username":"",item.id}
    })
    setTestList(prevOperators => [...prevOperators, ...operatorProfiles]);

  };



  const fetchSkill = async () => {
    const [listss] = await skill_list(page + 1);
    console.log("fetchSkill-----------", listss.results)
    // setFunction(listss.results)
    setSkills(listss.results);
  };

  const skill_Set = () => {
    skills.map((item) => {
      // if(item.id===)
      console.log("match skill", item.id)
      // let obj = {"username":"",item.id}
    })

  }

  function setFunction(listss) {
    let abc = listss.map((item) => ({
      id: item.id,
      title: item.skill_name,
      render: (rowData) => { console.log("tble rowDatarowData-------------- ", rowData, "item.id", item.id) }
    }));
    setSkills(abc)
    console.log("tble head ", abc)
  }
  const UploadTest = async () => {
    const payload = {
      "test_name": data.testname,
      // "test_description": "descr",
      "test_type": data.tranning_type,
      "skill": data.skill,
      "skill_level": parseInt(data.level),
      "maximum_questions": parseInt(data.totalQuestion),
      // "passing_criteria": '',
      "alloted_minutes": parseInt(data.time)
    }
    await createTest(payload)
    setModalOpen(false)
    await fetchTestList()
  }

  const postQuestion = async (finalQuestions) => {
    const quesIds = finalQuestions.map(question => question.id)
    const payload = {
      questions: quesIds,
    }
    await addQuestion(testId, payload)
    setData({})
    setModalOpen(false)
    await fetchTestList()
  }

  const handleAssign = async () => {
    await assignExam(data)
    setModalOpen(false)
    setData({})
  }

  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    await update(id, payload);
    // await fetchCompany();
    setLoading(false);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center' }}>
          {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
          <h1 className="mt-4">
            Skill Matrix
            {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
          </h1>
        </Stack>     
      </div>
      <Stack sx={{flexDirection:'row',gap:2}}>
              <Stack sx={{ width: 120}}>
                <PieChart 
                chartColors={['#AA4A44', COLORS.GRAY, COLORS.GRAY, COLORS.GRAY]}
                chart={{series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }]}}
                />
                <Typography sx={{fontSize:12}} textAlign='center'>Induction</Typography>
              </Stack>
              <Stack sx={{ width: 120}}>
                <PieChart 
                chartColors={['#AA4A44', '#008FFB', COLORS.GRAY, COLORS.GRAY]}
                chart={{series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }]}}
                />
                <Typography  sx={{fontSize:12}} textAlign='center'>Level 1</Typography>
              </Stack>
              <Stack sx={{ width: 120}}>
                <PieChart 
                chartColors={['#AA4A44', '#008FFB', '#00E396', COLORS.GRAY]}
                chart={{series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }]}}
                />
                <Typography  sx={{fontSize:12}} textAlign='center'>Level 2</Typography>
              </Stack>
              <Stack sx={{ width: 120}}>
                <PieChart 
                chartColors={['#AA4A44', '#008FFB', '#00E396', '#775DD0']}
                chart={{series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }]}}
                />
                <Typography  sx={{fontSize:12}} textAlign='center'>Level 3</Typography>
              </Stack>
        </Stack>
        <Stack sx={{py:1}}>

        <Input placeholder={"Search By Aadhar Number"}/>
        </Stack>
      <TableContainer>
        <MTable
        // sx={{ minWidth: minWidth }}
        // className={`${tableClasses}`}
        // size={dense && "small"}
        >
          <TableHead
            sx={{ backgroundColor: COLORS.BG_GRAY }}
          >
            <TableRow>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                S No.
              </TableCell>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                Aadhar
              </TableCell>
              {skills.map((item) => {
                return (<TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black',pl:5}}>
                  {item.skill_name}
                </TableCell>
                )
              })}
            </TableRow>
          </TableHead>
          <Stack sx={{p:0.2}}></Stack>
          <TableBody>
            {testList.map((item, index) => (
              <TableBodyRow row={item} arr={skills} index={index} />
            ))}
          </TableBody>

        </MTable>
      </TableContainer>

      {/* <Table
          columns={skills}
          rows={testList}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={total}
         /> */}


    </div>
  );

  function TableBodyRow({ row, arr, index }) {

    return (
      <>
      <Stack sx={{p:0.3}}></Stack>
      <TableRow
        key={row._id}
        sx={{ backgroundColor: COLORS.LG_GRAY }}
      >
        <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
          {index + 1}
        </TableCell>
        <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
          {row.aadhar}
        </TableCell>
        {
          arr.map((item, index) => {
            let color_1 = ['#AA4A44', COLORS.GRAY, COLORS.GRAY, COLORS.GRAY]
            let color_2 = ['#AA4A44', '#008FFB', COLORS.GRAY, COLORS.GRAY]
            let color_3 = ['#AA4A44', '#008FFB', '#00E396', COLORS.GRAY]
            let color_4 = ['#AA4A44', '#008FFB', '#00E396', '#775DD0']
            let color_5 = [COLORS.GRAY, COLORS.GRAY, COLORS.GRAY, COLORS.GRAY]

            return (<TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black',p:0,px:1 }}>
              <Stack sx={{ width: 120}}>
                <PieChart 
                chartColors={row[item.id]===0?color_1:row[item.id]===1?color_2:row[item.id]===2?color_3:row[item.id]===3?color_4:color_5}
                chart={{series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }]}}
                />
              </Stack>
            </TableCell>
            )
          })
        }


      </TableRow>
      </>
    )
  }
};

export default SkillMatrix;
