import React, { useEffect, useState } from "react";
import Modal from "../../components/modal";
import {
  SCHEMA_TYPES,
  SERIALIZATION_OPTIONS,
  VALIDATORS,
  UNITS
} from "../../helpers/constants";
import { Step, StepLabel, Stepper } from "@material-ui/core";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import JsonCreator from "../../components/UI/JsonCreator";
import { Validate } from "../../helpers/utils";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import useMaster from "../../store/master";

const EditToolMaterial = ({
  open,
  onClose,
  data,
  setData,
  onSubmit,
  isTool,
}) => {
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(new Map());
  const [loading, setLoading] = useState(false);
  const { categories } = useMaster();

  const TOOL_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "SAP Id",
      placeholder: "Enter SAP Id",
      type: SCHEMA_TYPES.INPUT,
      name: "sap_id",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "HSN CODE",
      placeholder: "HSN CODE",
      type: SCHEMA_TYPES.INPUT,
      name: "hsn_code",
      // validator: VALIDATORS.REQUIRED,
    },
    // {
    //   title: "Remaining Stock",
    //   placeholder: "Enter total remaining stock",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "remaining_stock",
    //   validator: VALIDATORS.REQUIRED,
    // },
    {
      title: "Vendor part ID",
      placeholder: "vendor part id",
      type: SCHEMA_TYPES.INPUT,
      name: "vendor_part_id",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Category",
      placeholder: "Select Category",
      type: SCHEMA_TYPES.MASTER,
      name: "category",
      validator: VALIDATORS.REQUIRED,
      fetch: [categories],
      noSearch:true
    },
    {
      title: "Unit",
      placeholder: "",
      type: SCHEMA_TYPES.SELECT,
      name: "unit",
      validator: VALIDATORS.REQUIRED,
      keyValues: UNITS
    },
    {
      title: "Cost",
      placeholder: "cost",
      type: SCHEMA_TYPES.INPUT,
      name: "cost",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Expected Life",
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: "expected_life",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Serialization",
      placeholder: "Select the serialization type",
      type: SCHEMA_TYPES.SELECT,
      name: "serialization_method",
      validator: VALIDATORS.NUMBER,
      keyValues: SERIALIZATION_OPTIONS,
    },
  ];

  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(TOOL_SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 1) {
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 2) {
      setLoading(true);
      await onSubmit();
      setLoading(false);
      setStep(0);
    }
  };

  const cleanup = () => {
    setErrors(new Map());
    setData({ location: {}, specification: {} });
    setStep(0);
    onClose();
  };

  const handleBack = () => {
    if (step === 0) {
      cleanup();
    } else {
      setStep((prev) => prev - 1);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  useEffect(() => {
    if (!isTool) cleanup();
  }, [isTool]);

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,80vw)"
    >
      <h3 className="opacity-80">EDIT {isTool ? "TOOL" : "MATERIAL"}</h3>
      <Stepper activeStep={step} alternativeLabel>
        {["Info", "Specifications", "Location"].map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      {step === 0 && (
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={TOOL_SCHEMA}
        />
      )}
      {step === 1 && (
        <JsonCreator
        setItemss={data?.specification}
          setJson={(value) =>
            setData((prev) => ({ ...prev, specification: value }))
          }
          title="Specification"
        />
      )}
      {step === 2 && (
        <JsonCreator
        setItemss={data?.location}
          setJson={(value) => setData((prev) => ({ ...prev, location: value }))}
          title="Location"
        />
      )}
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          {step === 2 ? "Edit" : "Next"}
        </Button>
      </Stack>
    </Modal>
  );
};

export default EditToolMaterial;
