import React, { useRef, useState, useEffect } from "react";
import Button from "../../../components/button"; // Adjust the import path as needed
import QRCode from "react-qr-code";
import { useScanning } from "../../../store/materialPlanner";

const NewStockTransferLabelDisplayModal = ({
  onClose,
  onScan,
  noStepFn,
  nextStepFn,
  newLabel
}) => {
  const printRef = useRef();
  const handlePrint = () => {
    window.print();
  };
  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "1rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <p>Stock Transfer Success!</p>
        <p>Please paste the following new label on the scanned packet.</p>
        <p>
          <strong>${newLabel}</strong>
        </p>
        <div
          ref={printRef}
          className="printable-content"
          style={{ display: "flex", gap: "2vw" }}
        >
          {" "}
          <span>Stock Transfer Label</span>
          <div
            style={{
              height: "auto",
              margin: "0 auto",
              maxWidth: 64,
              width: "100%",
            }}
          >
            {" "}
            <QRCode
              size={256}
              style={{ height: "auto", maxWidth: "100%", width: "100%" }}
              value={newLabel}
              viewBox={`0 0 256 256`}
            />{" "}
          </div>{" "}
        </div>
        <Button
          onClick={handlePrint}
          color="blue"
          width="16rem"
          height="2.5rem"
        >
          Print Label
        </Button>
        <p>
          Click Issue Material button below to scan the generated label and
          issue the corresponding material for this order.
        </p>
        <Button onClick={nextStepFn} color="green" width="8rem" height="2.5rem">
          Issue Material
        </Button>
      </div>
    </div>
  );
};

export default NewStockTransferLabelDisplayModal;
