import { IconButton, Stack, Tab, Tabs } from "@mui/material";
import React, { useEffect, useState,useRef } from "react";
import { useDispatch } from "react-redux";
import Button from "../../components/button";
import Table from "../../components/table";
import { Delete, Search } from "@mui/icons-material";
import useTools from "../../store/tools";
import useMaterials from "../../store/materials";
import AddToolMaterialInbound from "./AddToolMaterialInbound";
import AddQuantity from "./AddQuantity";
import Input from "../../components/input";
import Select from "../../components/input/Select";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import {setItem} from "../../store/materials/itemReducer"
import { toast } from "react-toastify";

const StoreInbound = () => {
  const dispatch = useDispatch()
  const childRef = useRef()
  const [dataList, setDataList] = useState([]);
  const [genCodes,setGenCodes] = useState()
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [tab, setTab] = useState(0);
  const [data, setData] = useState({});
  const [searchBy, setSearchBy] = useState('');
  const [searchInput,setSearchInput] = useState('')
  const {  list, create,createInbound,genBarCode,searchList} = useTools();
  const { list: list2, create: create2,createInbound:createInbound2 ,searchlistMat} = useMaterials();
  const [selectedRow,setSelectedRow] = useState({})
 

  useEffect(() => {
    fetchList();
  }, [page, tab]);

  const fetchList = async (name,val) => {
    setLoading("fetch");
    const [_list, _count] =
      tab === 1 ? await list2(page + 1) : await list(page + 1);
    setDataList(_list);
    setTotal(_count);
    setLoading(false);
  };

  const sList = async (val) => {
    if(searchBy !==""){
    setLoading("fetch");
    setTimeout(async() => {
      const [_list, _count] =
      tab === 1 ? await searchlistMat(page + 1,searchBy,val) : await searchList(page + 1,searchBy,val);
    setDataList(_list);
    setTotal(_count);
    }, 1000);

    setLoading(false);
    }else{
      toast.warn('Please Select Search by Option')
    }
  };

  const handleSubmit = async (slug,payload) => {
    // const payload = {
    //   ...data
    // };
    // console.log("ashisah----",payload,slug)
     tab === 1 ? await createInbound2(payload,slug): await createInbound(payload,slug)
    await fetchList()
    setData({});
    setModalOpen(false);
  };
  const onGenerate = async (payload,slug) => {
   const codes =  await genBarCode(payload,slug)
   setGenCodes(codes)
   if(codes.length){
    setTimeout(() => {
      childRef.current?.handlePrintq()
    }, 1000);
    }
  };

  const COLUMNS = [
    // {
    //   id: 2,
    //   title: "ID",
    //   render: (rowData) => rowData.id,
    // },
    {
      id: 3,
      title: "Name",
      render: (rowData) => rowData.name,
    },
    {
      id: 4,
      title: "HSN Code",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 5,
      title: "Vendor Part ID",
      render: (rowData) => rowData.vendor_part_id,
    },
    {
      id: 'cat',
      title: "Category",
      render: (rowData) => rowData.category,
    }, 
    {
      id: 6,
      title: "Serialization",
      render: (rowData) =>rowData.serialization_method===1?'No Serialization':rowData.serialization_method===2?"Use Pre-Generated ID":rowData.serialization_method===3?"Use Own ID":"-",
    },
    {
      id: 7,
      title: "Details",
      render: (rowData) => rowData.details,
    },  
    {
      id: 9,
      title: "Cost",
      render: (rowData) => rowData.cost,
    },
    {
      id:10,
      title:"Quantity",
      render:(rowData) => rowData.remaining_stock
    },
    {
      id: 10,
      title: "Expected Life",
      render: (rowData) => rowData.expected_life,
    },
    {
      id: 11,
      title: "Actions",
      render: (rowData) => (
        <IconButton sx={{ height: 25, width: 25 }} onClick={()=>{setModalOpen('add'); if(rowData.serialization_method===2){childRef.current?.handleFocus()};dispatch(setItem(rowData))}}>
          <AddCircleIcon color="primary" fontSize="small" />
        </IconButton>
      ),
    },
  ];
  
  const SEARCH = [
    {
      id: 'name',
      title: "Name"
    },
    {
      id: 'sap_id',
      title: "SAP ID",
    },
    {
      id: 'hsn_code',
      title: "HSN CODE"
    }, 
    {
      id: 'internal_id',
      title: "Internal ID",
    },
    {
      id: 'serialization',
      title: "Serialization",
    },
    {
      id: 'category',
      title: "Category",
    },  
   
  ];
  
  
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Inbound
          <span className="fs-30 ml-1 c-gray">({total})</span>
        </h1>
      </div>
      <div className="flex items-center justify-between mb-3">
        <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}}>
          {["Tools", "Materials"].map((label, i) => (
            <Tab key={i} className="fw-500" label={label} value={i} />
          ))}
        </Tabs>
        <Stack direction="row" justifyContent="flex-end">
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => {setSearchBy(e.target.value)}}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            onChange={(e)=>{setSearchInput(e.target.value);sList(e.target.value)}}
            icon={<Search onClick={()=>sList()} sx ={{cursor:'pointer'}}
            />}
          />
        </Stack>
      </div>
      <Table
        columns={COLUMNS}
        rows={dataList}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      {/* <AddToolMaterialInbound
        onClose={() => setModalOpen(false)}
        open={modalOpen === "add"}
        data={data}
        onSubmit={handleSubmit}
        setData={setData}
        isTool={tab === 0}
      /> */}
      <AddQuantity
      row={selectedRow}
      onClose={()=>setModalOpen(false)}
      open={modalOpen === 'add'}
      data={data}
      onSubmit={handleSubmit}
      setData={setData}
      isTool={tab=== 0}
      ref={childRef}
      onGenerate={onGenerate}
      codes={genCodes}
      // handleFocus={abc}
      />
    </div>
  );
};

export default StoreInbound;
