import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import useQuestion from "../../store/questionBank";
import useUser from "../../store/users";
import useMatrix from "../../store/skillmatrix";

const AddUserReport = ({ open, onClose, data, setData, onSubmit,onSubmit2, loading ,setSelectedQuestions,skills,users,testList}) => {

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);

  const {getPerformanceSkill,getPerformanceId} = useMatrix()

  const fetchUserProfile = async (id)=>{
    let tempId = id
    let payload ={
      "operator":id
    }
    let res = await getPerformanceSkill(payload)
    let resPer = await getPerformanceId(payload)
    console.log("getPerformanceRecord----->>>>",resPer[0].id)
    if(res){
      if(res.skill_level===3){
        setData({...data,skill_level:res.skill_level,skill:res.skill,operator:tempId,skill_id:resPer[0].id})
      }else{
        setData({...data,skill_level:res.skill_level+1,skill:res.skill,operator:tempId,skill_id:resPer[0].id})
      }
  }
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    if(name ==='operator'){
      console.log("operator id ",value)
      fetchUserProfile(value)
    }
  };
  
  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      // await onSubmit()
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 1) {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit2()
      return;
    }
  };


  const SKILL = [
    step === 0 &&( {
      title: "Operators",
      placeholder: "Select operator",
      type: SCHEMA_TYPES.SELECT,
      name: 'operator',
      validator: VALIDATORS.REQUIRED,
      keyValues:users,
    }),
    step === 0 &&( {
      title: "Skill",
      placeholder: "Select Skill",
      type: SCHEMA_TYPES.INPUT,
      name: 'skill',
      disabled:true
      // keyValues:skills,
      // inputType:'number',
      // validator: VALIDATORS.REQUIRED,
    }),
    step === 0 &&({
      title: "Skill Level",
      placeholder: "Select skill level",
      type: SCHEMA_TYPES.INPUT,
      name: 'skill_level',
      disabled:true
      // validator: VALIDATORS.REQUIRED,
      // keyValues:[{value:'Level 1',key:1},{value:"Level 2",key:2},{value:"Level 3",key:3}]
    }),
    step === 1 &&( {
      title: "Date",
      type: SCHEMA_TYPES.DATE,
      name: 'plan_date',
      validator: VALIDATORS.REQUIRED,
      // keyValues:users,
    }),
    step === 1 &&( {
      title: "Plan",
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: 'plan',
      inputType:'number',
      validator: VALIDATORS.REQUIRED,
    }),
    step === 1 &&( {
      title: "Actual",
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      inputType:'number',
      name: 'actual',
      validator: VALIDATORS.REQUIRED,
    }),
   ];

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">Add Performance</h3>
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={SKILL}
        />     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          Next
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddUserReport;
