import { useLocation, useNavigate } from "react-router-dom";
import { Stack, Typography ,Grid} from "@mui/material"
import moment from "moment"
import { useEffect, useLayoutEffect, useState } from "react"
import TimelineBar from "../timeline-bar"
import Table from "../../components/table";
import Select from "../../components/input/Select";
import InputDate from "../../components/input/indexDate";
import Input from "../../components/input";

const LiveView=()=>{
    const location = useLocation()
    const navigate = useNavigate()
    const [users,setUsers] = useState([])
    const [searchBy, setSearchBy] = useState('')
    const [searchInput, setSearchInput] = useState('')
    const [data, setData] = useState({
        "shift":'',
        "state":"",
        "quality":"",
      })
   
      const COLUMNS = [
        {
          id: 42,
          title: "Division",
          render: (rowData) => rowData.name,
        },
        {
          id: 1,
          title: "Line",
          render: (rowData) => rowData.sap_id,
        },
        {
          id: 2,
          title: "OEE",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 3,
          title: "Availability",
          render: (rowData) =>rowData.serialization_method===1?'No Serialization':rowData.serialization_method===2?"Use Pre-Generated ID":rowData.serialization_method===3?"Use Own ID":"-",
        },
        {
          id: 4,
          title: "Performance",
          render: (rowData) => rowData.vendor_part_id,
        },
        {
          id: 5,
          title: "Quality",
          render: (rowData) => rowData?.category,
        },
        {
          id: 6,
          title: "Total Cycle Time",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 7,
          title: "Total Downtime",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 8,
          title: "Total Available Time",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 9,
          title: "Pass",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 10,
          title: "Fail",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 11,
          title: "Status",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 12,
          title: "Last Event Received At",
          render: (rowData) => rowData.hsn_code,
        },
        {
          id: 13,
          title: "Details",
          render: (rowData) => rowData.hsn_code,
        }
      ];
  
    
      const SHIFT = [
        {
          id: 'All',
          title: "All"
        },
        {
          id: 'Shift A',
          title: "Shift A",
        },
        {
          id: 'Shift B',
          title: "Shift B",
        },
        {
          id: 'Out of Shift',
          title: "Out of Shift",
        }
      ]
      const STATE = [
        {
          id: 'All',
          title: "All"
        },
        {
          id: 'Production',
          title: "Production",
        },
        {
          id: 'Downtime',
          title: "Downtime",
        }
      ]
      const QUALITY = [
        {
          id: 'All',
          title: "All"
        },
        {
          id: 'Good',
          title: "Good",
        },
        {
          id: 'NG',
          title: "NG",
        }
      ]

      const SEARCH = [
        {
          id: 'Division',
          title: "Division"
        },
        {
          id: 'Line',
          title: "Line",
        },
        {
          id: 'OEE',
          title: "OEE"
        }, 
        {
          id: 'Availability',
          title: "Availability",
        },
        {
          id: 'Performance',
          title: "Performance",
        },
        {
          id: 'Quality',
          title: "Quality",
        }, 
        {
          id: 'Total Cycle Time',
          title: "Total Cycle Time",
        }, 
        {
          id: 'Total Downtime',
          title: "Total Downtime",
        },
        {
          id: 'Total Available Time',
          title: "Total Available Time",
        },
        {
          id: 'Pass',
          title: "Pass",
        },
        {
          id: 'Fail',
          title: "Fail",
        },
        {
          id: 'Status',
          title: "Status",
        },
       
      ];
    
    return(
        <div className="mx-3 my-2">
        <h1 className="mt-2">
         Live View
        </h1>
        <Stack sx={{flexDirection:'row',justifyContent:'space-between',mb:2}}>
            <Stack sx={{flexDirection:'row'}}>
            <Select
                keyValues={SHIFT.map((d) => ({
                key: d?.id,
                value: d?.title,
                }))}
                width="120px"
                placeholder="Shift"
                classes="mr-1"
                value={data.shift}
                onChange={(e) => setData({...data,shift:e.target.value})}
            />
            <Select
                keyValues={STATE.map((d) => ({
                key: d?.id,
                value: d?.title,
                }))}
                width="120px"
                placeholder="State"
                classes="mr-1"
                value={data.state}
                onChange={(e) =>  setData({...data,state:e.target.value})}
            />
            <Select
                keyValues={QUALITY.map((d) => ({
                key: d?.id,
                value: d?.title,
                }))}
                width="120px"
                placeholder="Quality"
                classes="mr-1"
                value={data.quality}
                onChange={(e) =>  setData({...data,quality:e.target.value})}
            />
            <InputDate  width={'fit-content'}/>
            
            </Stack>
            <Stack direction="row" justifyContent="flex-end">
            <Select
                keyValues={SEARCH.map((d) => ({
                key: d?.id,
                value: d?.title,
                }))}
                width="120px"
                placeholder="Search by"
                classes="mr-1"
                value={searchBy}
                onChange={(e) => setSearchBy(e.target.value)}
            />
            <Input
                height="37px"
                placeholder="Universal Search"
                value={searchInput}
                onChange={(e)=>{setSearchInput(e.target.value)}}
            />
            </Stack>
        </Stack>

        <TimelineBar/>

        <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={5}
        titleCellClasses="c-gray fw-700"
        serialize
      />
        </div>
    )
}

export default LiveView;