import React, { useRef, useState, useEffect } from "react";
import Button from "../../../components/button"; // Adjust the import path as needed
import { useScanning } from "../../../store/materialPlanner";

const SupervisorLoginModal = ({ onClose, onScan, noStepFn, nextStepFn }) => {
  const [formData, setFormData] = useState({ username: "", password: "" });
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Form Data:", formData); // You can add further processing here, such as sending the data to a server
    nextStepFn(formData.username, formData.password);
  };
  return (
    <div className="action_modal__container">
      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <p>Supervisor Login is required to confirm partial submission.</p>
        <p>Please Login.</p>
        <form
          onSubmit={handleSubmit}
          style={{
            gap: "2.5rem",
            display: "flex",
            flexDirection: "column",
          }}
        >
          {" "}
          <div>
            {" "}
            <label htmlFor="username">Username:</label>{" "}
            <input
              type="text"
              id="username"
              name="username"
              value={formData.username}
              onChange={handleChange}
              required
            />{" "}
          </div>{" "}
          <div>
            {" "}
            <label htmlFor="password">Password:</label>{" "}
            <input
              type="password"
              id="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              required
            />{" "}
          </div>{" "}
          <div
            style={{
              gap: "2.5rem",
              display: "flex",
            }}
          >
            <Button
              onClick={noStepFn}
              white={true}
              width="8rem"
              height="2.5rem"
              bordered={true}
              customStyle={{
                border: "1px solid #3E3E3E",
                backgroundColor: "#FFFFFF",
              }}
            >
              Cancel
            </Button>{" "}
            <Button
              onClick={handleSubmit}
              color="green"
              width="8rem"
              height="2.5rem"
            >
              Submit
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SupervisorLoginModal;
