import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";

const AddUser = ({ open, onClose, data, setData, onSubmit, loading ,tab,edit,selectSkill}) => {
  
  const USER_SCHEMA = [
   edit!==true &&( {
      title: "Skill Name",
      placeholder: "Enter SkillName",
      type: SCHEMA_TYPES.INPUT,
      name: "skill_name",
      validator: VALIDATORS.REQUIRED,
    }),
    // {
    //   title: "Duration",
    //   placeholder: "Number of Days",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "fullname",
    //   validator: VALIDATORS.REQUIRED,
    // },
    // {
    //   title: "Performance Criteria",
    //   placeholder: "Performance Criteria",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "performance_criteria",
    //   validator: VALIDATORS.REQUIRED,
    // },
  ];

  // const addSupervisor =[
  //   {
  //     title: "Supervisor",
  //     placeholder: "Select Supervisor",
  //     type: SCHEMA_TYPES.SELECT,
  //     name: "username",
  //     multi:true,
  //     height:'auto',
  //     keyValues:[{value:'super A',key:"super A"},{value:"super B",key:"super B"},{value:"super C",key:"super C"}]
  //   }
  // ]


  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  console.log("selectSkill",selectSkill)

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(USER_SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit()
      onClose()
      // setStep((prev) => prev + 1);
      return;
    } 
  };

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(600px,90vw)"
    >
      <h3 className="opacity-80">{edit===true?"Edit":"Add"} Skill</h3>
      
      {edit &&(<h3 className="opacity-80">{data.skill_name}</h3>)}
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          // schema={step ===0?USER_SCHEMA:addSupervisor}
          schema={USER_SCHEMA}
        />
        {step ===0 &&(
        <>
        <Stack sx={{}}>
          <Typography sx={{mt:1,fontWeight:'bold'}} >Level 1</Typography>
            <Stack sx={{flexDirection:'row',mt:-1}}>
              <Input type='number' title={'Duration'} value={data.l1_duration} placeholder='in days' width={'90%'} onChange={(e)=>setData((prev) => ({ ...prev, "l1_duration": e.target.value }))}/>
              <Input type='number' title={'Exam Passing Criteria'} value={data.l1_exam} placeholder='Exam Passing Criteria' width={'90%'} onChange={(e)=>setData((prev) => ({ ...prev, "l1_exam": e.target.value }))}/>
              <Input type='number' title={'Floor Work Criteria'} value={data.l1_floor} placeholder='Floor Work Criteria' width={'90%'} onChange={(e)=>setData((prev) => ({ ...prev, "l1_floor": e.target.value }))}/>
              <Input type='number' title={'No of Entry'} value={data.l1_performance} placeholder='No of Entry' width={'90%'} onChange={(e)=>setData((prev) => ({ ...prev, "l1_performance": e.target.value }))}/>
            </Stack>
        </Stack>
        <Stack sx={{}}>
          <Typography sx={{mt:1,fontWeight:'bold'}} >Level 2</Typography>
            <Stack sx={{flexDirection:'row',mt:-1}}>
              <Input type='number' title={'Duration'} placeholder='in days' width={'90%'} value={data.l2_duration} onChange={(e)=>setData((prev) => ({ ...prev, "l2_duration": e.target.value }))}/>    
              <Input type='number' title={'Exam Passing Criteria'} placeholder='Exam Passing Criteria' width={'90%'} value={data.l2_exam} onChange={(e)=>setData((prev) => ({ ...prev, "l2_exam": e.target.value }))}/>
              <Input type='number' title={'Floor Work Criteria'} placeholder='Floor Work Criteria' width={'90%'} value={data.l2_floor} onChange={(e)=>setData((prev) => ({ ...prev, "l2_floor": e.target.value }))}/>
              <Input type='number' title={'No of Entry'} value={data.l2_performance} placeholder='No of Entry' width={'90%'} onChange={(e)=>setData((prev) => ({ ...prev, "l2_performance": e.target.value }))}/>
           
            </Stack>
        </Stack>
        <Stack sx={{}}>
          <Typography sx={{mt:1,fontWeight:'bold'}} >Level 3</Typography>
            <Stack sx={{flexDirection:'row',mt:-1}}>
              <Input type='number' title={'Duration'} placeholder='in days' width={'90%'} value={data.l3_duration} onChange={(e)=>setData((prev) => ({ ...prev, "l3_duration": e.target.value }))}/>   
              <Input type='number' title={'Exam Passing Criteria'} placeholder='Exam Passing Criteria' width={'90%'} value={data.l3_exam} onChange={(e)=>setData((prev) => ({ ...prev, "l3_exam": e.target.value }))}/>
              <Input type='number' title={'Floor Work Criteria'} placeholder='Floor Work Criteria' width={'90%'} value={data.l3_floor} onChange={(e)=>setData((prev) => ({ ...prev, "l3_floor": e.target.value }))}/>
              <Input type='number' title={'No of Entry'} value={data.l3_performance} placeholder='No of Entry' width={'90%'} onChange={(e)=>setData((prev) => ({ ...prev, "l3_performance": e.target.value }))}/>
            </Stack>
        </Stack>
        </>
        )}
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          Next
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddUser;
