import React from "react";
import {Stack} from '@mui/material';


const ButtonTag = ({
  children,
  onClick,
  variant,
  top,
  colorCode,
  classes,
  height,
  width,
  bordered,
  gray,
  disabled,
  loading,
  hide,
  background,
  white,
}) => {

  return (
    <Stack onClick={onClick} sx={{cursor:'pointer',borderRadius:5,textTransform:'capitalize',justifyContent:'center',alignItems:'center',px:0.4,fontSize:18,fontWeight:400,backgroundColor:variant==="outline"?'transparent':colorCode,color:variant==="outline"?"#3E3E3E":"white",border:variant==="outline"?1:'none',borderColor:colorCode}}>{children}</Stack>
  );
};

export default ButtonTag;