import React,{useState} from "react";
import Modal from "../../components/modal";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import Checkbox from '@mui/material/Checkbox';

const Add = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Plant",
      placeholder: "Select Model",
      type: SCHEMA_TYPES.SELECT,
      name: "Line",
      validator: VALIDATORS.REQUIRED,
      keyValues: [],
    },
    {
      title: "Start Time",
      placeholder: "select time",
      type: SCHEMA_TYPES.TIME,
      name: "start_time",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "End Time",
      placeholder: "select time",
      type: SCHEMA_TYPES.TIME,
      name: "end_time",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Overlap",
      placeholder: "Select Overlap",
      type: SCHEMA_TYPES.SELECT,
      name: "plant",
      validator: VALIDATORS.REQUIRED,
      keyValues:[]
    },
    {
      title: "Type",
      placeholder: "Select Plant",
      type: SCHEMA_TYPES.RADIO  ,
      name: "plant",
      validator: VALIDATORS.REQUIRED,
      keyValues:[{"value":"working_shift","label":"Working Shift"},{"value":"planned_break","label":"Planned Break"}]
    }
   
  ];
  const [errors, setErrors] = useState(new Map());
  const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">Add Shift Schedule</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
        <Stack sx={{mt:1}}>
          <Typography>Week Days</Typography>
            <Stack sx={{flexDirection:'row',gap:1}}>
              <Checkbox {...label} icon={<Typography>M</Typography>} checkedIcon={<Typography>M</Typography>} />
              <Checkbox {...label} icon={<Typography>T</Typography>} checkedIcon={<Typography>T</Typography>} />
              <Checkbox {...label} icon={<Typography>W</Typography>} checkedIcon={<Typography>W</Typography>} />
              <Checkbox {...label} icon={<Typography>T</Typography>} checkedIcon={<Typography>T</Typography>} />
              <Checkbox {...label} icon={<Typography>F</Typography>} checkedIcon={<Typography>F</Typography>} />
              <Checkbox {...label} icon={<Typography>S</Typography>} checkedIcon={<Typography>S</Typography>} />
              <Checkbox {...label} icon={<Typography>S</Typography>} checkedIcon={<Typography>S</Typography>} />
            </Stack>
        </Stack>
     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
