import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useSkill from "../../store/skill";
import useUser from "../../store/users"
import Button from "../../components/button";
import AddSkill from "./AddSkill";
import AddReport from "./AddReport"
import AddSupervisor from './AddSupervisor'
import { toast } from "react-toastify";
import { IconButton, Stack ,Tab, Tabs, Typography} from "@mui/material";
import { Delete } from "@mui/icons-material";
import Tooltip from '@mui/material/Tooltip';
import EditIcon from '@mui/icons-material/Edit';
import Category from "../../components/UI/Category";
import UploadCsv from "../store/UploadCsv";
import AddCircleIcon from '@mui/icons-material/AddCircle';

const SkillPage = () => {
  const [tab , setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [skill, setSkill] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [csvData, setCsvData] = useState([])
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [data, setData] = useState({
  });
  const { list, create,updateSkill,update,addSupervisor, remove,opratorList,createOperator,removeOperator,updateOperator } = useSkill();
  const [edit , setEdit] = useState(false)
  const [skillId,setSkillId] = useState('')
 
  const { list:fetchUser } = useUser();
  const [selectSkill, setSelectSkill] = useState()

  useEffect(() => {
    fetchSkill();
  }, [page, tab]);

  useEffect(() => {
    fetchUsers();
  }, []);

  const fetchUsers = async () => {
    setLoading("fetch");
    const [_list, _count] =  await fetchUser(page + 1)
    // const newOptr = _list.filter(item => item.is_operator);
    const newUsr = _list.filter(item => !item.is_operator);
    const newUsers = newUsr.map(item => ({
      value: item.username,
      key: item.id
    }));
    setUsers(prevUsers => [...prevUsers,...newUsers]);
  };

  const fetchSkill = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1)
    // console.log("_list--------------",_list)
    setSkill(_list.results);
    setTotal(_count);
    setLoading(false);
  };

const handleAdd = async () => { 
    const payload = {
      "skill_name": data.skill_name,
      "test_performance_criteria": [data.l1_exam,data.l2_exam,data.l3_exam ],
      "duration_between_two_tests": [data.l1_duration,data.l2_duration,data.l3_duration ],
      "floor_performance_criteria": [
                parseInt(data.l1_floor),
                parseInt(data.l2_floor),
                parseInt(data.l3_floor)
              ],
      "no_of_performance_entries": [
                parseInt(data.l1_performance),
                parseInt(data.l2_performance),
                parseInt(data.l3_performance)
              ]
  }

    setLoading("fetch")
  //  let res = await create(payload)
   let res = await updateSkill(payload,skillId)
   await fetchSkill()
    setLoading(false);
  };

  async function assignSupervisor (){
    const payload = {...data}
    const payload2 ={
      "add":payload.supervisor
    }
    await addSupervisor(skillId,payload2)
    setModalOpen(false);
    await fetchSkill();
  }

  const handleDelete = async (id) => {
    setLoading("fetch");
    tab === "Users" ? await remove(id): await removeOperator(id);
    await fetchUsers();
    setLoading(false);
  };

  const handleUpdate = async (id,is_admin,type) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    const payload2 ={
      "status":is_admin===true?false:true
    }
    tab === "Users" ? await update(id,type==="status"?payload2:payload):updateOperator(id,type==="status"?payload2:payload)
    await fetchUsers();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Skill Name",
      render: (rowData) => rowData.skill_name,
    },
    {
      id: 112,
      title: "No. of Enteries",
      render: (rowData) => rowData.no_of_performance_entries.length,
    },
    {
      id: 6,
      title: "Actions",
      render: (rowData) => (<>
        <Stack flexDirection={'row'} sx={{gap:1}}>
        
        <Tooltip title="add supervisor"> <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => {setEdit(true);setModalOpen("addSuper");setSkillId(rowData.id)}}
        >
        <AddCircleIcon color="primary" fontSize="small" />
        </IconButton>
        </Tooltip>
        <Tooltip title="edit skill"> 
          <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => {setEdit(true);setModalOpen("add");setSelectSkill(rowData);
            setData({ ...data,
              skill_name:rowData.skill_name,
              l1_duration:rowData.duration_between_two_tests[0],
              l2_duration:rowData.duration_between_two_tests[1],
              l3_duration:rowData.duration_between_two_tests[2],
              l1_exam:rowData.test_performance_criteria[0],
              l2_exam:rowData.test_performance_criteria[1],
              l3_exam:rowData.test_performance_criteria[2],
              l1_floor:rowData.floor_performance_criteria[0],
              l2_floor:rowData.floor_performance_criteria[1],
              l3_floor:rowData.floor_performance_criteria[2],
              l1_performance:rowData.no_of_performance_entries[0],
              l2_performance:rowData.no_of_performance_entries[1],
              l3_performance:rowData.no_of_performance_entries[2],
            });
            setSkillId(rowData.id)}}
        >
        <EditIcon color="primary" fontSize="small" />
        </IconButton>
        </Tooltip>

        {/* <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton> */}
      </Stack>
        </>
      ),
    },
  ];

  const handleSubmitCSV = async()=>{
     csvData.map(async(csvitem)=> {
      if(tab === "Users"){
        if(csvitem.email!==""){
          await create(csvitem)
        }else{
          return
        }
      }else{
        if(csvitem.employee_id!==""){
          await createOperator(csvitem)
        }else{
          return
        }
      }
     })
     setModalOpen(false);
     await fetchSkill(page =1)
  }

  return (
    <div className="mx-3 my-2 fw-500">
      <div className="flex items-center justify-between">
        <Typography className="mt-2 fw-500">
          Skill's List<span className="fw-500 ml-1 ">({skill.length})</span>
        </Typography>
        <Stack justifyContent="flex-end" direction="row" sx={{mb:1}}>
        <Button onClick={() => {setModalOpen("add");setData({})}}>Add New Skill</Button>
        </Stack>
      </div>
      <Table
        columns={COLUMNS}
        rows={skill}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddSkill
      selectSkill={selectSkill}
        open={modalOpen === "add"}
        onClose={() => {setModalOpen(false);setEdit(false)}}
        data={data}
        setData={setData}
        onSubmit={handleAdd}
        loading={loading === "add"}
        edit={edit}
      />
      <AddSupervisor
        open={modalOpen === "addSuper"}
        onClose={() => {setModalOpen(false);setEdit(false)}}
        data={data}
        setData={setData}
        onSubmit={assignSupervisor}
        loading={loading === "add"}
        user={users}
      />
      <AddReport
        open={modalOpen === "Report"}
        onClose={() => setModalOpen(false)}
        onSubmit={handleAdd}
      />
      <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csvUser"}
        onSubmit={handleSubmitCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
        tab={tab}
      />
    </div>
  );
};

export default SkillPage;
