import React,{useState} from "react";
import { useSelector } from "react-redux";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import JsonFormatter from "../../components/UI/JsonFormatter";
import moment from "moment";


const OrderDetail = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const row = useSelector((state) => state?.OrderItem?.data)

  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Sales Order Number",
      placeholder: "Enter Sales Order Number",
      type: SCHEMA_TYPES.INPUT,
      name: "sales_order_number",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Target Warehouse",
      placeholder: "Enter Target Warehouse",
      type: SCHEMA_TYPES.INPUT,
      name: "target_warehouse",
      validator: VALIDATORS.REQUIRED,
    },
   
  ];


  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">ORDER DETAILS</h3> 
     <Stack sx={{gap:1}}>
        <Stack sx={{flexDirection:'row',gap:5}}>
          <Typography sx={{width:'20%'}}>Order ID :</Typography>
          <Typography sx={{fontWeight:'bold'}}>{row?.order_id}</Typography>
        </Stack>
        <Stack sx={{flexDirection:'row',gap:5}}>
          <Typography sx={{width:'20%'}}>Client :</Typography>
          <Typography sx={{fontWeight:'bold'}}>{row?.target_warehouse}</Typography>
        </Stack>
        <Stack sx={{flexDirection:'row',gap:5}}>
          <Typography sx={{width:'20%'}}>Oty :</Typography>
          <Typography sx={{fontWeight:'bold'}}>{row?.planned_quantity}</Typography>
        </Stack>
        <Stack sx={{flexDirection:'row',gap:5}}>
          <Typography sx={{width:'20%'}}>Start Date :</Typography>
          <Typography sx={{fontWeight:'bold'}}>{moment(row?.created).format('ll')}</Typography>
        </Stack>
        <Stack sx={{flexDirection:'row',gap:5}}>
          <Typography sx={{width:'20%'}}>End Date :</Typography>
          <Typography sx={{fontWeight:'bold'}}>{moment(row?.modified).format('ll')}</Typography>
        </Stack>
     </Stack>
    </Modal>
  );
};

export default OrderDetail;
