import { IconButton, Stack, Tab, Tabs } from "@mui/material";
import React, { useEffect, useState,useRef } from "react";
import { useDispatch } from "react-redux";
import Button from "../../components/button";
import Table from "../../components/table";
import { Delete, Search } from "@mui/icons-material";
import useOrder from "../../store/order";
import useJob from "../../store/order/job"
import Input from "../../components/input";
import Select from "../../components/input/Select";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import {setItem} from "../../store/materials/itemReducer";
import moment from "moment";
import AddChild from "./AddChild";
import { useLocation, useNavigate } from "react-router-dom";
import DriveFolderUploadIcon from '@mui/icons-material/DriveFolderUpload';
import ButtonTag from "../../components/buttonTag";

const SubOrder = () => {
  const navigate = useNavigate()
  const location = useLocation()
  const dispatch = useDispatch()
  const [orderID, setOrderID] = useState(location.state)
  const [dataList, setDataList] = useState([]);
  const [genCodes,setGenCodes] = useState()
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [tab, setTab] = useState("Sub Orders");
  const [data, setData] = useState({});
  const [searchBy, setSearchBy] = useState();
  const {  list,subList, createSub,createInbound,genBarCode } = useOrder();
  const {  jobList,createJob} = useJob();
//   const { list: list2, create: create2,createInbound:createInbound2 } = useMaterials();
  const [selectedRow,setSelectedRow] = useState({})
 

  useEffect(() => {
    fetchList();
  }, [page, tab,orderID]);

  const fetchList = async () => {
    setLoading("fetch");
    const [res] = tab === "Sub Orders"? await subList(orderID,page + 1):await jobList(page + 1,orderID);
    setDataList(res);
    setTotal(res.count);
    setLoading(false);
  };

  const handleSubmit = async () => {
    const payload = {
      ...data
    };
    tab === "Sub Orders"?await createSub(orderID,payload):await createJob(payload,orderID)
    await fetchList()
    setData({});
    setModalOpen(false);
  };
 

  const COLUMNS = [
    {
      id: 1,
      title: tab === "Sub Orders"?"Order ID":"Name",
      render: (rowData) =>tab === "Sub Orders"? rowData.order_id :rowData.name,
    },
    {
      id: 2,
      title: "Client ID",
      render: (rowData) => rowData.vendor_part_id,
    },
    {
      id: 3,
      title: "Oty",
      render: (rowData) => rowData.planned_quantity,
    }, 
    {
      id: 4,
      title: "Start Date",
      render: (rowData) => moment(rowData.created).format('ll'),
    },
    {
      id: 5,
      title: "End Date",
      render: (rowData) =>moment(rowData.created).format('ll')
     },
     {
      id: 7,
      title: "Status",
      render: (rowData) => <ButtonTag colorCode={'#1535DB'} onClick={()=>{tab === "Sub Orders" && setOrderID(rowData.id)}}>To Plan</ButtonTag>
      
      // <Button onClick={()=>{tab === "Sub Orders" && setOrderID(rowData.id)}} bordered>To Plan</Button>
  },
    {
      id: 6,
      title: "Actions",
      render: (rowData) => (
      <IconButton onClick={()=>navigate('/plan-overview',{state:rowData.order_id})}>
        <DriveFolderUploadIcon/>
      </IconButton>
      ),
    },
   
  ];
  
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          {tab}
          <span className="fs-30 ml-1 c-gray">({dataList.length})</span>
        </h1>
        {/* <Button onClick={()=>setModalOpen("add")}>add</Button> */}
      </div>
      <div className="flex items-center justify-between mb-3">
        <Tabs value={tab} onChange={(_e, v) => setTab(v)}>
          {["Sub Orders", "Jobs"].map((label, i) => (
            <Tab key={i} label={label} value={label} />
          ))}
        </Tabs>
        {/* <Stack direction="row" justifyContent="flex-end">
          <Select
            keyValues={COLUMNS.slice(0, COLUMNS?.length - 1).map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => setSearchBy(e.target.value)}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            icon={<Search />}
          />
          
        </Stack> */}
        <Button onClick={()=>setModalOpen("add")}>Add {tab}</Button>
      </div>
      <Table
        columns={COLUMNS}
        rows={dataList}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddChild
        onClose={() => setModalOpen(false)}
        open={modalOpen === "add"}
        data={data}
        onSubmit={handleSubmit}
        setData={setData}
        tab={tab}
      />
    </div>
  );
};

export default SubOrder;
