import React, { useEffect, useState } from "react";
import { Route, Routes as AllRoutes } from "react-router-dom";
import TenantsMainPage from "./pages/tenants";
import UserMainPage from "./pages/users";
import { ROLE } from "./helpers/constants";
import { useDispatch, useSelector } from "react-redux";
import { REDUCER_TYPES } from "./store";
import { resetAuth } from "./store/api/utils";
import MasterList from "./pages/store/StoreMaster";
import StoreInbound from "./pages/store/StoreInbound";
import StoreOutbound from "./pages/store/StoreOutbound";
import OutboundHistory from "./pages/store/OutboundHistory";
import StoreOrder from "./pages/store/Order";
import Error from "./pages/error";
import MachinePage from "./pages/machine/index";
import CompanyPage from "./pages/company/index";
import PlantPage from "./pages/plant/index";
import DivisionPage from "./pages/division";
import LinePage from "./pages/line";
import AssetPage from "./pages/asset";
import GroupsPage from "./pages/groups";
import UserTenantPage from "./pages/usersTenant";
import Dashboard from "./pages/dashboard";
import Dash from "./pages/dash";
import TimelineBar from "./pages/timeline-bar";
import DowntimePage from "./pages/downtime";
import ModalMasterPage from "./pages/model_master";
import PartsRejectionMode from "./pages/partsRejection";
import ShiftMaster from "./pages/shiftMaster";
import Notification from "./pages/notification";
import ConsumablePage from "./pages/consumable";
import OgProfile from "./pages/og_profile";
import Order from "./pages/order/orderPage";
import SubOrder from "./pages/order/Plan";
import PlanOverview from "./pages/order/PlanOverview";
import LiveView from "./pages/LiveView";
import OperatorPortal from "./pages/operatorPortal";
import Test from "./pages/test";
import TestUpload from "./pages/uploadTest";
import SkillPage from "./pages/skills";
import QuestionBank from "./pages/questionBank";
import SkillMatrix from "./pages/skillMatrix";
import TestPortal from "./pages/testPortal";
import TrainingPage from "./pages/planTraining";
import TestDetail from "./pages/test/testDetail";
import AddReport from "./pages/report";
import PlannerOverview from "./pages/materialPlanner/overview";
import OrderInstruction from "./pages/materialPlanner/plan";
import ScanningPage from "./pages/materialPlanner/plan/scanningPage";

const Routes = () => {
    const dispatch = useDispatch();
    const [hydrated, setHydrated] = useState(false);
    const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
    const role = state?.user?.data?.role;

    const routes = [
        {
            path: "/report",
            element: <AddReport />,
        },
        {
            path: "/TestDetail",
            element: <TestDetail />,
        },
        {
            path: "/plan-training",
            element: <TrainingPage />,
        },
        {
            path: "/test-portal",
            element: <TestPortal />,
        },
        {
            path: "/skill-matrix",
            element: <SkillMatrix />,
        },
        {
            path: "/live-view",
            element: <Dash />,
        },
        {
            path: "/test",
            element: <Test />,
        },
        {
            path: "/test-upload",
            element: <TestUpload />,
        },
        {
            path: "/skill-page",
            element: <SkillPage />,
        },
        {
            path: "/question-bank",
            element: <QuestionBank />,
        },
        {
            path: "/machine-view",
            element: <LiveView />,
        },
        {
            path: "/operator-portal",
            element: <OperatorPortal />,
        },
        {
            path: "/timelinebar",
            element: <TimelineBar />,
        },
        {
            path: "/Dashboardd",
            element: <Dashboard />,
        },
        {
            path: "/error",
            element: <Error />,
        },
        {
            path: "/users",
            element: role === ROLE.ADMINISTRATOR ? <UserMainPage /> : <Error />,
        },
        {
            path: "/tenants",
            element: role === ROLE.SUPERADMIN ? <TenantsMainPage /> : <Error />,
        },
        {
            path: "/store-master",
            element: role === ROLE.ADMINISTRATOR ? <MasterList /> : <Error />,
        },
        {
            path: "/downtime-master",
            element: role === ROLE.ADMINISTRATOR ? <DowntimePage /> : <Error />,
        },
        {
            path: "/model-master",
            element:
                role === ROLE.ADMINISTRATOR ? <ModalMasterPage /> : <Error />,
        },
        {
            path: "/parts-rejection",
            element:
                role === ROLE.ADMINISTRATOR ? (
                    <PartsRejectionMode />
                ) : (
                    <Error />
                ),
        },
        {
            path: "/shift-master",
            element: role === ROLE.ADMINISTRATOR ? <ShiftMaster /> : <Error />,
        },
        {
            path: "/notification",
            element: role === ROLE.ADMINISTRATOR ? <Notification /> : <Error />,
        },
        {
            path: "/consumable",
            element:
                role === ROLE.ADMINISTRATOR ? <ConsumablePage /> : <Error />,
        },
        {
            path: "/profile",
            element: role === ROLE.ADMINISTRATOR ? <OgProfile /> : <Error />,
        },
        {
            path: "/store-inbound",
            element: <StoreInbound />,
        },
        {
            path: "/store-outbound",
            element: <StoreOutbound />,
        },
        {
            path: "/store-outbound-history",
            element: <OutboundHistory />,
        },
        {
            path: "/settings",
            element: <h1>Settings</h1>,
        },
        {
            path: "/logout",
            element: <h1>Log</h1>,
        },
        {
            path: "/order",
            element: <StoreOrder />,
        },
        {
            path: "/machinepage",
            element: <MachinePage />,
        },
        {
            path: "/companypage",
            element: role === ROLE.ADMINISTRATOR ? <CompanyPage /> : <Error />,
        },
        {
            path: "/plantpage",
            element: role === ROLE.ADMINISTRATOR ? <PlantPage /> : <Error />,
        },
        {
            path: "/divisionpage",
            element: role === ROLE.ADMINISTRATOR ? <DivisionPage /> : <Error />,
        },
        {
            path: "/line",
            element: role === ROLE.ADMINISTRATOR ? <LinePage /> : <Error />,
        },
        {
            path: "/asset",
            element: role === ROLE.ADMINISTRATOR ? <AssetPage /> : <Error />,
        },
        {
            path: "/group",
            element: role === ROLE.ADMINISTRATOR ? <GroupsPage /> : <Error />,
        },
        {
            path: "/tenant-Users",
            element: role === ROLE.SUPERADMIN ? <UserTenantPage /> : <Error />,
        },
        {
            path: "/running-order",
            element: <Order />,
        },
        {
            path: "/plan",
            element: <SubOrder />,
        },
        {
            path: "/plan-overview",
            element: <PlanOverview />,
        },
        {
            path: "/material-planner",
            element: <PlannerOverview />,
        },
        {
            path: "/material-planner-plan",
            element: <OrderInstruction />,
        },
        {
            path: "/material-planner-plan/scanning",
            element: <ScanningPage />,
        },
    ];
    useEffect(() => {
        resetAuth(dispatch, state);
        setHydrated(true);
    }, []);

    if (!hydrated) {
        return null;
    }
    return (
        <AllRoutes>
            {routes.map(({ path, element }) => (
                <Route element={element} path={path} key={path} />
            ))}
        </AllRoutes>
    );
};

export default Routes;
