import { IconButton, Stack, Tab, Tabs, Typography } from "@mui/material";
import moment from "moment/moment";
import React, { useEffect, useState,useRef } from "react";
import { useDispatch } from "react-redux";
import Button from "../../components/button";
import Table from "../../components/table";
import { Delete, Search } from "@mui/icons-material";
import useTools from "../../store/tools";
import useMaterials from "../../store/materials";
import useOutbound from "../../store/storeoutbound"
import AddToolMaterialInbound from "./AddToolMaterialInbound";
import AddQuantity from "./AddQuantity";
import Input from "../../components/input";
import Select from "../../components/input/Select";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import {setSearch} from "../../store/storeoutbound/forSearch"
import { toast } from "react-toastify";
import styles from "../../components/input/input.module.scss"
import SearchableSelect from "../../components/input/SearchableSelect";

const StoreOutbound = () => {
  const dispatch = useDispatch()
  const childRef = useRef()
  const [dataList, setDataList] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
  const [tab, setTab] = useState(0);
  const [tab2, setTab2] = useState('Serialization');
  const [internalId, setInternalId] = useState('');
  const [searchBy, setSearchBy] = useState('');
  const [searchInput,setSearchInput] = useState('')
  const {  list, create,createInbound,genBarCode,searchList,getBarInfo,outBoundItem} = useTools();
  const { list: list2, create: create2,createInbound:createInbound2 ,searchlistMat,matOutBoundItem,getBarInfo:getMatbar} = useMaterials();
  const {fetchToolInbound,fetchMaterialInbound} = useOutbound()
 const [barcode, setBarcode] = useState('')
 const [scan, setScan] = useState([])
 const [barList ,setBarList] = useState([])
 

  useEffect(() => {
    if(tab2 === 'Serialization'){
      childRef.current.focus()
    }
  }, [page, tab]);

  const fetchList = async (name,val) => {
    setLoading("fetch");
    const [_list, _count] =
      tab === 1 ? await fetchMaterialInbound(name,val) : await fetchToolInbound(name,val);
    setBarList(_list)
    setLoading(false);
  };

  const handleSubmit = async () => {
    if(internalId!==""){
    barList.map( async (item,index)=> {
      if(tab2 === "No Serialization"){
        if(item.quantity){
          tab === 0 ? await outBoundItem(item,internalId): matOutBoundItem(item,internalId)
        }else{
          return
        }
      }else{
        tab === 0 ? await outBoundItem(item,internalId): matOutBoundItem(item,internalId)
      }
    })
    setBarList([])
    setScan([])
    setInternalId('')
  }else{
    toast.warn("Please Fill Internal Id")
  }
};
 
  const COLUMNS = [
    {
      id: 2,
      title: "Inbound Date",
      render: (rowData) => <Typography>{moment(rowData.created).format('LL')}</Typography>,
    },
    {
      id: 3,
      title: "Name",
      render: (rowData) => rowData.name,
    },
    {
      id: 1,
      title: "Barcode",
      render: (rowData) => rowData.barcode,
    },
    {
      id: 4,
      title: "Expected Life",
      render: (rowData) => rowData.expected_life,
    },
    {
      id: 6,
      title: "Total Stock",
      render: (rowData) =>tab2 === "No Serialization"? rowData.remaining_stock:1,
    },
    {
      id: 5,
      title: "Quantity ",
      render: (rowData) =>tab2 === "No Serialization"? <Input width={"15%"}
      onChange={(e)=>handleQuantity(e,rowData.id)}
      />:1,
    }
  ];

  const handleQuantity = (event, itemId) => {
    const { value } = event.target;
    // Create a new array with updated item
    const updatedItems = barList.map(item =>
      item.id === itemId ? { ...item, quantity: value } : item
    );
    // Update state with the new array
    setBarList(updatedItems);
  };
  const SEARCH = [
    {
      id: 'name',
      title: "Name"
    },
    {
      id: 'sap_id',
      title: "SAP ID",
    },
    {
      id: 'hsn_code',
      title: "HSN CODE"
    }, 
    {
      id: 'internal_id',
      title: "Internal ID",
    },
    {
      id: 'serialization',
      title: "Serialization",
    },
    {
      id: 'category',
      title: "Category",
    },   
  ];

  async function handleCode(val) {
    if (val.code === 'Enter') {
      if (!scan.includes(barcode)) {
        setScan([...scan, barcode]);
      const barData = tab === 1? await getMatbar(barcode):await getBarInfo(barcode)
      if(barData[0]){
        const tempItem = tab === 1?{"created":barData[0].material_lot.created,"name":barData[0].material_type.name,"id":barData[0].material_type.id,"expected_life":barData[0].material_type.expected_life,"barcode":barData[0].barcode} :{"created":barData[0].tool_lot.created,"name":barData[0].tool_type.name,"id":barData[0].tool_type.id,"expected_life":barData[0].tool_type.expected_life,"barcode":barData[0].barcode}
        setBarList([...barList,tempItem])
        // await outBoundItem(tempItem)
      }
  }
  setBarcode('')
  }
}

function SearchSelect(e) {
  const { value } = e.target;
  
  // Check if value is not a new entry
  if (!value.isNew) {
    // Check if barList has items
    if (barList.length > 0) {
      // Filter through barList
      const isExisting = barList.some(item => item.id === value.key);

      if (!isExisting) {
        // If item with value.key does not exist in barList, add new object
        const obj = {
          "created": value.created,
          "name": value.value,
          "id": value.key,
          "expected_life": value.expected_life,
          "quantity": '1'
        };
        setBarList([...barList, obj]);
      } else {
        // If item with value.key already exists, update barList
        setBarList([...barList]);
      }
    } else {
      // If barList is empty, add new object
      const obj = {
        "created": value.created,
        "name": value.value,
        "id": value.key,
        "expected_life": value.expected_life,
        "quantity": '1'
      };
      setBarList([...barList, obj]);
    }
  }
}

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Outbound
          {/* <span className="fs-30 ml-1 c-gray">({total})</span> */}
        </h1>
      </div>
      <div className=" items-center  mb-3 fw-500">
        <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}}>
          {["Tools", "Materials"].map((label, i) => (
            <Tab key={i} label={label} className="fw-500" value={i} />
          ))}
        </Tabs> 
      </div>
     <div className="flex items-center  mb-3">
     <Tabs value={tab2} onChange={(_e, v) => {setTab2(v)}}>
          {["Serialization", "No Serialization"].map((label, i) => (
            <Tab key={i} label={label} value={label} />
          ))}
        </Tabs>
     {tab2 === "No Serialization"?(
      <Stack direction="row" justifyContent="flex-end" ml={5}>
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => {setSearchBy(e.target.value)}}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            onChange={(e)=>{setSearchInput(e.target.value);fetchList(searchBy,e.target.value)}}
          />
        
         
        </Stack>
        ):(
        <input
        style={{marginLeft:50}}
        className={styles.input}
        ref={childRef} 
        height="17px"
        placeholder="Scan Code"
        value={barcode} 
        onKeyUp={(e) => handleCode(e)} 
        onChange={(e) => setBarcode(e.target.value)}     
      />
        )}
     </div>
      {barList.length>0&&(
        <>
        <Table
        columns={COLUMNS}
        rows={barList}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        // page={page}
        // setPage={setPage}
        // paginator
        // serialize
        // total={total}
      />
      <Stack sx={{mt:1,alignItems:'flex-end'}}>
        <Stack sx={{flexDirection:'row',gap:1}}>
      <Input
            placeholder="Internal Id"
            value={internalId}
            onChange={(e)=>{setInternalId(e.target.value)}}
          />
      <Button
        width={"fit-content"}
        onClick={handleSubmit}
        // onClick={()=>console.log("barList",barList)}
        loading={loading}>
        Save
      </Button>
      </Stack>
      </Stack>
      
      </>
      )}
    </div>
  );
};

export default StoreOutbound;
