import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useUser = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USERS}/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const listByUserName = async (name) => {
    const payload = {'username':name}
    const client = new REQUEST();
    const res = await client.post(
      `search-by-username/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    if (res?.data) {
      // console.log("res  ",res.data)
      return res?.data;
    }
    return [[]];
  };

  const listByAadhar = async (name) => {
    const payload = {'aadhar_number':name}
    const client = new REQUEST();
    const res = await client.post(
      `search-by-aadhar/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    if (res?.data) {
      // console.log("res  ",res.data)
      return res?.data;
    }
    return [[]];
  };


  const listByAadhar2 = async (name) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USERS}/`,
      {},
      headers,
      resolveResponse
    );
    if (res?.data) {
      const newOptr = res.data.filter(item => item.is_operator);
      return newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    }
    return [[], 0];
  };


  const userProfile = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      // console.log("res  ",res.data)
      return res?.data;
    }
    return [[], 0];
  };

  const opratorList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.OPERATORS}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data?.results.length>0) {
      console.log("res  ",res.data)
      return [res?.data.results,res.data.count];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.USERS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User created successfully!")
    );
    return res?.data;
  };

  const updateProfile = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}-${ENDPOINTS.PROFILE}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };

  const updateOpSkill = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.post(
      `training/update-skill/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Skill Updated successfully!")
    );
    return res?.data;
  };

  const createOperator = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.OPERATORS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User created successfully!")
    );
    return res?.data;
  };

  const update = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.USER}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "User Updated successfully!")
    );
    return res?.data;
  };
  const updateOperator = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.OPERATOR}/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Operator Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.USER}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "User deleted successfully!")
    );
    return res?.data;
  };
  const removeOperator = async (uuid) => {
    if (!uuid) return;
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.OPERATOR}/${uuid}/`,
      headers,
      (res) => resolveResponse(res, null, "Operator deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    create,
    userProfile,
    updateProfile,
    update,
    remove,
    opratorList,
    createOperator,
    updateOperator,
    removeOperator,
    listByUserName,
    listByAadhar,
    listByAadhar2,
    updateOpSkill
  };
};

export default useUser;
