
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import { styled, useTheme } from '@mui/material/styles';
import ReactApexChart from 'react-apexcharts';
import { Box, Typography } from '@mui/material';

// import { fNumber } from 'src/utils/format-number';

// ----------------------------------------------------------------------

const CHART_HEIGHT = 400;

const LEGEND_HEIGHT = 72;

// ----------------------------------------------------------------------

export default function SemiRadialBar({label,colorCode, chart, ...other }) {
  // const theme = useTheme();

  const { colors, series, options } = chart;

  const chartSeries = series.map((i) => i.value);

  const pOptions ={
      chart: {
        type: 'radialBar',
        // offsetY: -30,
        sparkline: {
          enabled: true
        }
      },
      plotOptions: {
        radialBar: {
          startAngle: -90,
          endAngle: 90,
          track: {
            background: "#e7e7e7",
            strokeWidth: '97%',
            // margin: 5, // margin is in pixels
            dropShadow: {
              enabled: true,
              // top: 2,
              left: 0,
              color: '#999',
              opacity: 1,
              blur: 2
            }
          },
          // hollow: {
          //   margin: 15,
          //   size: '70%',
          //   background: 'transparent',
          //   image: undefined
          // },
          dataLabels: {
            name: {
              show: true,
              fontSize: '18px',
              fontWeight: 600,
              color: colorCode,
              offsetY: 5
            },
            value: {
              show: true,
              fontSize: '24px',
              fontFamily: 'Helvetica, Arial',
              fontWeight: 400,
              color: colorCode,
              offsetY: -30,
              formatter: function (val) {
                return val + "%"; // Format value with a percent sign
              }
            }}
        }
      },
      grid: {
        padding: {
          top: 1
        }
      },
      fill: {
        type: 'solid',
        colors:[colorCode]
        // gradient: {
        //   shade: 'light',
        //   shadeIntensity: 0.4,
        //   inverseColors: false,
        //   opacityFrom: 1,
        //   opacityTo: 1,
        //   stops: [0, 50, 53, 91]
        // },
      },
      labels: [label],
    }
  
  return (
      <Box sx={{ display:'flex',justifyContent:'center'}} >
        <ReactApexChart type="radialBar" series={chartSeries} options={pOptions} />
      </Box>
  );
}
