import { IconButton, Stack, Tab, Tabs } from "@mui/material";
import React, { useEffect, useState,useRef } from "react";
import { useDispatch } from "react-redux";
import Button from "../../components/button";
import Table from "../../components/table";
import { Delete, Search } from "@mui/icons-material";
import useTools from "../../store/tools";
import useMaterials from "../../store/materials";
import AddToolMaterialInbound from "./AddToolMaterialInbound";
import AddQuantity from "./AddQuantity";
import Input from "../../components/input";
import Select from "../../components/input/Select";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import {setItem} from "../../store/materials/itemReducer"
import { toast } from "react-toastify";
import moment from "moment/moment";

const OutboundHistory = () => {
  const dispatch = useDispatch()
  const childRef = useRef()
  const [dataList, setDataList] = useState([]);
  const [genCodes,setGenCodes] = useState()
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [tab, setTab] = useState(0);
  const [data, setData] = useState({});
  const [searchBy, setSearchBy] = useState('');
  const [searchInput,setSearchInput] = useState('')
  const {  historyList,searchOutbound:toolSearch} = useTools();
  const { historyList:matHistory ,searchOutbound} = useMaterials();
  const [selectedRow,setSelectedRow] = useState({})
 

  useEffect(() => {
    fetchList();
  }, [page, tab]);

  const fetchList = async (name,val) => {
    setLoading("fetch");
    const [_list, _count] =
      tab === 1 ? await matHistory(page + 1) : await historyList(page + 1);
    setDataList(_list);
    setTotal(_count);
    setLoading(false);
  };

  const sList = async (val) => {
    if(searchBy !==""){
    setLoading("fetch");
    setTimeout(async() => {
      const [_list, _count] =
      tab === 1 ? await searchOutbound(page + 1,searchBy,val) : await toolSearch(page + 1,searchBy,val);
    setDataList(_list);
    setTotal(_count);
    }, 1000);

    setLoading(false);
    }else{
      toast.warn('Please Select Search by Option')
    }
  };

 
 

  const COLUMNS = [
   
    {
      id: 3,
      title: "Name",
      render: (rowData) => rowData.tool_type?.name ||rowData.material_type?.name,
    },
    {
      id: 4,
      title: "Bar Code",
      render: (rowData) => rowData.serial_number,
    },
    {
      id: 5,
      title: "Internal ID",
      render: (rowData) => rowData.internal_id,
    },
    {
      id: 6,
      title: "Category",
      render: (rowData) => rowData.tool_type?.category || rowData.material_type?.category,
    },
    {
      id: 1,
      title: "Inbound Date",
      render: (rowData) =>rowData.tool_lot?.created? moment(rowData.tool_lot.created).format('lll'): moment(rowData.material_lot?.created).format('lll'),
    },
    {
      id: 2,
      title: "Outbound Date",
      render: (rowData) => moment(rowData.created).format('lll'),
    },
    
    // {
    //   id: 11,
    //   title: "Actions",
    //   render: (rowData) => (
    //     <IconButton sx={{ height: 25, width: 25 }} onClick={()=>{setModalOpen('add'); if(rowData.serialization_method===2){childRef.current?.handleFocus()};dispatch(setItem(rowData))}}>
    //       <AddCircleIcon color="primary" fontSize="small" />
    //     </IconButton>
    //   ),
    // },
  ];
  
  const SEARCH = [
    {
      id: 'name',
      title: "Name"
    },
    {
      id: 'internal_id',
      title: "Internal ID",
    },
    {
      id: 'category',
      title: "Category",
    }
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Outbound History
          <span className="fs-30 ml-1 c-gray">({total})</span>
        </h1>
      </div>
      <div className="flex items-center justify-between mb-3">
        <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}}>
          {["Tools", "Materials"].map((label, i) => (
            <Tab key={i} label={label} className="fw-500" value={i} />
          ))}
        </Tabs>
        <Stack direction="row" justifyContent="flex-end">
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => {setSearchBy(e.target.value)}}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            onChange={(e)=>{setSearchInput(e.target.value);sList(e.target.value)}}
            icon={<Search onClick={()=>sList()} sx ={{cursor:'pointer'}}
            />}
          />
        </Stack>
      </div>
      <Table
        columns={COLUMNS}
        rows={dataList}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
     
    </div>
  );
};

export default OutboundHistory;
