import React, { useRef } from "react";

// components
import Button from "../../../components/button";
import QRCode from "react-qr-code";
// Assets
// const modalStateData = {
//   msg1: [
//     "Please break the packet into two parts ",
//     "Make first Packet with 500 Units ",
//     "Make second Packet with 700 Units",
//   ],
//   msg2: [
//     "Print the Bar Code and Paste",
//     "Barcode 1 on First packet",
//     "Barcode 2 on Second packet",
//   ],
// };
const LabelsGeneratedModal = ({
  qty1 = 0,
  qty2 = 0,
  label1 = "",
  label2 = "",
  noStepFn = () => {},
  nextStepFn = () => {},
}) => {
  const parentRef = useRef();
  const handlePrint = () => {
    window.print();
  };

  return (
    <div className="action_modal__container">
      <div>
        <img src={"/error_outline.svg"} alt="icon" />
      </div>
      <span>Please break the packet into 2 parts: </span>
      <span>
        First one with <strong>{qty1}</strong> units, and second one with{" "}
        <strong>{qty2}</strong> units.{" "}
      </span>
      <span>
        Then, please paste label: <strong>{label1}</strong> on the first packet,
      </span>

      <span>
        and label: <strong>{label2}</strong> on the second packet.{" "}
      </span>

      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          gap: "1vw",
        }}
      >
        <div
          ref={parentRef}
          className="printable-content"
          style={{ display: "flex", gap: "2vw" }}
        >
          {" "}
          <span>First Packet Label</span>
          <div
            style={{
              height: "auto",
              margin: "0 auto",
              maxWidth: 64,
              width: "100%",
            }}
          >
            {" "}
            <QRCode
              size={256}
              style={{ height: "auto", maxWidth: "100%", width: "100%" }}
              value={label1}
              viewBox={`0 0 256 256`}
            />{" "}
          </div>{" "}
          <span>Second Packet Label</span>
          <div
            style={{
              height: "auto",
              margin: "0 auto",
              maxWidth: 64,
              width: "100%",
            }}
          >
            {" "}
            <QRCode
              size={256}
              style={{ height: "auto", maxWidth: "100%", width: "100%" }}
              value={label2}
              viewBox={`0 0 256 256`}
            />{" "}
          </div>{" "}
        </div>
        <Button
          onClick={handlePrint}
          color="blue"
          width="16rem"
          height="2.5rem"
        >
          Print Labels
        </Button>
      </div>

      <div
        className="material-planner-layout-footer"
        style={{
          gap: "2.5rem",
        }}
      >
        <Button
          onClick={nextStepFn}
          color="green"
          width="16rem"
          height="2.5rem"
        >
          I have pasted new labels
        </Button>
      </div>
    </div>
  );
};

export default LabelsGeneratedModal;
