
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import { styled, useTheme } from '@mui/material/styles';
import ReactApexChart from 'react-apexcharts';
import { Box, Typography,Stack } from '@mui/material';

// import { fNumber } from 'src/utils/format-number';

// ----------------------------------------------------------------------

const CHART_HEIGHT = 400;

const LEGEND_HEIGHT = 72;

// ----------------------------------------------------------------------

export default function FullRadialBar({ title, colorCode, chart, ...other }) {
  // const theme = useTheme();

  const { series, options } = chart;

  const chartSeries = series.map((i) => i.value);

  const fOptions ={
      chart: {
        type: 'radialBar',
        // offsetY: -30,
        sparkline: {
          enabled: true
        }
      },
      plotOptions: {
        radialBar: {
          track: {
            color:colorCode,
            background: "#e7e7e7",
            strokeWidth: '85%',
            margin: 5, // margin is in pixels
            dropShadow: {
              enabled: true,
              // top: 2,
              left: 0,
              color: '#999',
              opacity: 1,
              blur: 1
            }
          },
          hollow: {
            size: '50%'
          },
          dataLabels: {
            show:true,
            name: {
              show: false
            },
            value: {
              show: true,
              fontSize: "14px",
              color: colorCode,
              offsetY: 5,
              // formatter: function (val) {
              //   return val + "%"; // Format value with a percent sign
              // }
            }}
        }
      },
      grid: {
        padding: {
          top: 1
        }
      },
      stroke: {
        lineCap: 'round'
    },
      fill: {
        type: 'solid',
        colors:[colorCode]
        // gradient: {
        //   shade: 'light',
        //   shadeIntensity: 0.4,
        //   inverseColors: false,
        //   opacityFrom: 1,
        //   opacityTo: 1,
        //   stops: [0, 50, 53, 91]
        // },
      }

    }
  
  return (
      <Stack sx={{}} dir="ltr">
        <ReactApexChart type="radialBar" series={chartSeries} options={fOptions} />
      </Stack>
  );
}
