import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import Button from "../../components/button";
import useTools from "../../store/tools";
import { Chip, IconButton, Stack, Tab, Tabs } from "@mui/material";
import { Checklist, Delete, Place, Search } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import JsonFormatter from "../../components/UI/JsonFormatter";
import UploadCsv from "./UploadCsv";
import useMaterials from "../../store/materials";
import AddToolMaterial from "./AddToolMaterial";
import EditToolMaterial from "./EditToolMaterial"
import Select from "../../components/input/Select";
import Input from "../../components/input";
import useMaster from "../../store/master";
import Category from "../../components/UI/Category";
import { toast } from "react-toastify";

const MasterList = () => {
  const [dataList, setDataList] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [csvData, setCsvData] = useState([])
  const [tab, setTab] = useState(0);
  const [searchBy, setSearchBy] = useState('');
  const [searchInput,setSearchInput] = useState('')
  const [edit ,setEdit] = useState(false)
  const [data, setData] = useState({
    specification: {},
    location: {},
  });
  const { list, create, remove, createInbound: createInboundTool,update,searchList } = useTools();
  const {
    list: list2,
    create: create2,
    remove: remove2,
    update:update2,
    createInbound,
    searchlistMat
  } = useMaterials();
  const { createCategory ,categories:getCategory} = useMaster();

  useEffect(() => {
    fetchTools();
  }, [page, tab]);

  const fetchTools = async () => {
    setLoading("fetch");
    const [_list, _count] =
      tab === 1 ? await list2(page + 1) : await list(page + 1);
    setDataList(_list);
    setTotal(_count);
    setLoading(false);
  };

  const sList = async (val) => {
    if(searchBy!== ''){
    setLoading("fetch");
    setTimeout(async() => {
      const [_list, _count] =
      tab === 1 ? await searchlistMat(page + 1,searchBy,val) : await searchList(page + 1,searchBy,val);
    setDataList(_list);
    setTotal(_count);
    }, 1000);
    
    setLoading(false);
    }else{
      toast.warn('Please Select Search by Option')
    }
  };
  const handleSubmit = async () => {
    
    let categoryId = data?.category?.key;
    if (data?.category?.isNew) {
      const res = await createCategory(categoryId);
      categoryId = res?.id;
    }
    const payload = {
      ...data,
      category: categoryId,
    };
    const res = tab === 1 ? await create2(payload) : await create(payload);
    if (res?.id) {
      // tab === 1
      //   ? await createInbound(res?.id)
      //   : await createInboundTool(res?.id);
      setData({
        specification: {},
        location: {},
      });
      setModalOpen(false);
      await fetchTools();
    }
  };

  const handleSubmitCSV = async()=>{
   const catlist = await getCategory()
   var catId =''
    csvData.map(async(csvitem)=> {
      catlist.map(async(item)=>{
        if(csvitem.category === item.value){
           catId = item.key
        }  
      })
      console.log("pre catID",catId)
      if(catId === "" && catId === undefined){
        const res = await createCategory(csvitem.category);
        catId = res?.id
      }
      csvitem.category = catId
      console.log(" final item",csvitem)
      tab === 1 ? await create2(csvitem) : await create(csvitem)
    })
    setModalOpen(false);
    setTimeout(async() => {
      await fetchTools()
    }, 500);
  }
  const handleDelete = async (id) => {
    setLoading("fetch");
    tab === 1 ? await remove2(id) : await remove(id);
    await fetchTools();
    setLoading(false);
  };

  const handleEdit = async (id) => {
    const payload = {...data};
    setLoading("fetch");
    tab === 1 ? await update2(payload) : await update(payload);
    setModalOpen(false);
    await fetchTools();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 42,
      title: "Name",
      render: (rowData) => rowData.name,
    },
    {
      id: 1,
      title: "SAP ID",
      render: (rowData) => rowData.sap_id,
    },
    {
      id: 2,
      title: "HSN CODE",
      render: (rowData) => rowData.hsn_code,
    },
    {
      id: 3,
      title: "Serialization",
      render: (rowData) =>rowData.serialization_method===1?'No Serialization':rowData.serialization_method===2?"Use Pre-Generated ID":rowData.serialization_method===3?"Use Own ID":"-",
    },
    {
      id: 4,
      title: "Vendor Part ID",
      render: (rowData) => rowData.vendor_part_id,
    },
    {
      id: 5,
      title: "Category",
      render: (rowData) => <Category title={rowData?.category} />,
    },
    {
      id: 6,
      title: "Specifications",
      render: (rowData) => (
        <JsonFormatter
          icon={<Checklist fontSize="small" />}
          json={rowData?.specification}
        />
      ),
    },
    // {
    //   id: 7,
    //   title: "Remaining Stock",
    //   render: (rowData) => rowData.remaining_stock,
    // },
    {
      id: 8,
      title: "Location",
      render: (rowData) => (
        <JsonFormatter
          icon={<Place fontSize="small" />}
          json={rowData?.location}
        />
      ),
    },
    {
      id: 9,
      title: "Actions",
      render: (rowData) => (
        <Stack sx={{flexDirection:'row'}}>
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
        <Delete color="error" fontSize="small" />
        </IconButton>
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={()=>{setModalOpen("edit");setData(rowData);setData((prev) => ({ ...prev, ['category']: {'value':rowData.category} }));}}
        >
        <EditIcon color="primary" fontSize="small" />
        </IconButton>
        </Stack>
      ),
    },
  ];
  const SEARCH = [
    {
      id: 'name',
      title: "Name"
    },
    {
      id: 'sap_id',
      title: "SAP ID",
    },
    {
      id: 'hsn_code',
      title: "HSN CODE"
    }, 
    {
      id: 'internal_id',
      title: "Internal ID",
    },
    {
      id: 'serialization',
      title: "Serialization",
    },
    {
      id: 'category',
      title: "Category",
    },  
   
  ];
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Master
          <span className="fs-30 ml-1 c-gray">({total})</span>
        </h1>
        <Stack justifyContent="flex-end" direction="row">
          <Button onClick={() => setModalOpen("csv")} bordered classes="mr-1">
            Upload CSV
          </Button>
          <Button onClick={() => setModalOpen("add")}>Create</Button>
        </Stack>
      </div>
      <div className="flex items-center justify-between">
        <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}} className="mb-2">
          {["Tools", "Materials"].map((label, i) => (
            <Tab label={label} className="fw-500" value={i} />
          ))}
        </Tabs>
        <Stack direction="row" justifyContent="flex-end">
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => setSearchBy(e.target.value)}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            onChange={(e)=>{setSearchInput(e.target.value);sList(e.target.value)}}
            icon={<Search sx={{cursor:'pointer'}} onClick={()=>sList()} />}
          />
        </Stack>
      </div>
      <Table
        edit={edit}
        columns={COLUMNS}
        rows={dataList}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddToolMaterial
        onClose={() => setModalOpen(false)}
        open={modalOpen === "add"}
        data={data}
        onSubmit={handleSubmit}
        setData={setData}
        isTool={tab === 0}
      />
      <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csv"}
        onSubmit={handleSubmitCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
      />
      <EditToolMaterial
        onClose={() => setModalOpen(false)}
        open={modalOpen === "edit"}
        data={data}
        onSubmit={handleEdit}
        setData={setData}
        isTool={tab === 0}/>
    </div>
  );
};

export default MasterList;
