import { IconButton, Stack, Tab, Tabs, Typography } from "@mui/material";
import React, { useEffect, useState,useRef } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { Step, StepLabel, Stepper } from "@material-ui/core";
import Button from "../../components/button";
import Table from "../../components/table";
import { Delete, Padding, Search } from "@mui/icons-material";
import useOrder from "../../store/order";
import Input from "../../components/input";
import Select from "../../components/input/Select";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import {setItem} from "../../store/materials/itemReducer";
import moment from "moment";
import AddOrder from "./AddOrder";
import OrderDetail from "./OrderDetail";
import { setOrder } from "../../store/order/itemOrder";
import ButtonTag from '../../components/buttonTag'
import {COLORS} from '../../helpers/constants'

const RunningOrder = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const childRef = useRef()
  const [dataList, setDataList] = useState([]);
  const [genCodes,setGenCodes] = useState()
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [tab, setTab] = useState(0);
  const [data, setData] = useState({});
  const [step, setStep] = useState(2)
  const [searchBy, setSearchBy] = useState();
  const {  list, create,createInbound,genBarCode } = useOrder();
//   const { list: list2, create: create2,createInbound:createInbound2 } = useMaterials();
  const [selectedRow,setSelectedRow] = useState({})
 

  useEffect(() => {
    fetchList();
  }, [page, tab]);

  const fetchList = async () => {
    setLoading("fetch");
    const [res] = await list(page + 1);
    console.log("counnt----",res.count)
    setDataList(res?.results);
    setTotal(res.count);
    setLoading(false);
  };

  const handleSubmit = async () => {
    const payload = {
      ...data
    };
    await create(payload)
    await fetchList()
    setData({});
    setModalOpen(false);
  };


  const COLUMNS = [
    {
      id: 1,
      title: "Order ID",
      render: (rowData) =><Typography sx={{cursor:'pointer',color:'blue',fontSize:20}} onClick={()=>{dispatch(setOrder(rowData));setModalOpen('detail')}}>{rowData.order_id}</Typography>
    },
    {
      id: 2,
      title: "Client ID",
      render: (rowData) => rowData.vendor_part_id,
    },
    {
      id: 3,
      title: "Oty",
      render: (rowData) => rowData.planned_quantity,
    }, 
    {
      id: 4,
      title: "Start Date",
      render: (rowData) => moment(rowData.created).format('ll'),
    },
    {
      id: 5,
      title: "End Date",
      render: (rowData) =>moment(rowData.created).format('ll')
     },
    // {
    //   id: 6,
    //   title: "Actions",
    //   render: (rowData) => (
    //   <Button onClick={()=>navigate('/plan',{state:rowData.id})}>Running</Button>
    //   // <Button bordered onClick={()=>{dispatch(setOrder(rowData));setModalOpen('detail')}}>Running</Button>
    //   ),
    // },
    {
      id: 6,
      title: "Actions",
      render: (rowData) => (
      <ButtonTag variant={"outline"} colorCode={'#FA7474'} onClick={()=>navigate('/plan',{state:rowData.id})}>on hold</ButtonTag>
      // <Button bordered onClick={()=>{dispatch(setOrder(rowData));setModalOpen('detail')}}>Running</Button>
      ),
    },
    {
        id: 7,
        title: "Status",
        render: (rowData) =>
          <Stepper  style={{padding:0,backgroundColor:COLORS.LG_GRAY}}
         activeStep={step} alternativeLabel>
        {["I", "S", "L"].map((label) => (
          <Step key={label}>
            <StepLabel />
          </Step>
        ))}
      </Stepper>
    }
  ];
  
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Order
          <span className="fs-30 ml-1 c-gray">({total})</span>
        </h1>
        <Button onClick={()=>setModalOpen("add")}>Add Order</Button>
      </div>
      <div className="flex items-center justify-between mb-3">
        {/* <Tabs value={tab} onChange={(_e, v) => setTab(v)}>
          {["Tools", "Materials"].map((label, i) => (
            <Tab key={i} label={label} value={i} />
          ))}
        </Tabs> */}
        {/* <Stack direction="row" justifyContent="flex-end">
          <Select
            keyValues={COLUMNS.slice(0, COLUMNS?.length - 1).map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            value={searchBy}
            onChange={(e) => setSearchBy(e.target.value)}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            icon={<Search />}
          />
        </Stack> */}
      </div>
      <Table
        columns={COLUMNS}
        rows={dataList}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddOrder
        onClose={() => setModalOpen(false)}
        open={modalOpen === "add"}
        data={data}
        onSubmit={handleSubmit}
        setData={setData}
      />
      <OrderDetail
        onClose={() => setModalOpen(false)}
        open={modalOpen === "detail"}
        data={data}
        onSubmit={handleSubmit}
        setData={setData}
      />
    </div>
  );
};

export default RunningOrder;
