import React, { useEffect, useState, useRef, useImperativeHandle,forwardRef } from "react";
import { useReactToPrint } from 'react-to-print';
import Barcode from 'react-barcode';
import Modal from "../../components/modal";
import {
  SCHEMA_TYPES,
  SERIALIZATION_OPTIONS,
  VALIDATORS,
  UNITS
} from "../../helpers/constants";
import styles from '../../components/input/input.module.scss'
import { Step, StepLabel, Stepper } from "@material-ui/core";
import { Stack, Typography, Grid } from "@mui/material";
import Divider from '@mui/material/Divider';
import Button from "../../components/button";
import JsonCreator from "../../components/UI/JsonCreator";
import { Validate } from "../../helpers/utils";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { useSelector } from "react-redux";

const SCHEMA = [
  // {
  //   title: "Unit",
  //   placeholder: "unit",
  //   type: SCHEMA_TYPES.SELECT,
  //   name: "unit",
  //   validator: VALIDATORS.REQUIRED,
  //   keyValues:UNITS
  // },
  // {
  //   title: "Present Quanitity",
  //   placeholder: "",
  //   type: SCHEMA_TYPES.INPUT,
  //   name: "presentQuanitity",
  //   validator: VALIDATORS.REQUIRED,
  // },
  // {
  //   title: "New Quanitity",
  //   // placeholder: "Enter vendor part Id",
  //   type: SCHEMA_TYPES.COUNTER,
  //   name: "quantity",
  //   value:1,
  //   validator: VALIDATORS.REQUIRED,
  // } 
];

const AddQuantity = forwardRef(({
  open,
  onClose,
  data,
  setData,
  onSubmit,
  isTool,
  onGenerate,
  codes
},ref) => {
  const row = useSelector((state) => state?.InboundItem?.data)
  const inputRef = useRef(null);
  const printRef = useRef(null)
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(new Map());
  const [loading, setLoading] = useState(false);
  const [scan, setScan] = useState([])
  const [bar, setBar] = useState('')
  const [quantity , setQuantity] = useState('')


  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 1) {
      setStep((prev) => prev + 1);
      return;
    }
    if (step === 2) {
      setLoading(true);
      await onSubmit();
      setLoading(false);
      setStep(0);
    }
  };

  const cleanup = () => {
    setErrors(new Map());
    setData({});
    setStep(0);
    onClose();
  };

  const handleBack = () => {
    if (step === 0) {
      cleanup();
    } else {
      setStep((prev) => prev - 1);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleCounter = (val) => {
    if (val === 'add') {
      setData((prev) => ({ ...prev, [Object.keys(data)[0]]: Number(data.newQuanitity) + 1 }));
    } else {
      if (data.newQuanitity > 0) {
        setData((prev) => ({ ...prev, [Object.keys(data)[0]]: Number(data.newQuanitity) - 1 }));
      }
    }
  }
function handleFocus(){
        setTimeout(() => {
          inputRef.current.focus()  
       }, 500);
      
}
  useImperativeHandle(ref,()=>{
   return{
     handleFocus,
     handlePrintq
  }})

  useEffect(() => {
    if (!isTool) cleanup();
  }, [isTool]);

  function handleCode(val) {
    // setData((prev) => ({ ...prev, ['quantity']: scan.length}));
    if (val.code === 'Enter') {
      if (!scan.includes(bar)) {
        setScan([...scan, bar]);
      }
      setBar('')
      console.log("count---", data[0])
    }
  }
  const handlePrint=async()=>{
    const payload = {
      "quantity":Number(quantity)
    }
  await onGenerate(payload,row?.id)
  }

  const handlePrintq = useReactToPrint({
    content: () => printRef.current,
  //   print: async (iframe) => {
  //     // react-to-print would normally call `window.print` on the iframe
  //     // Instead, by passing this function, now you can do whatever you
  //     // want with the iframe, something like:
  //     // https://www.electronjs.org/docs/latest/api/web-contents#contentsgetprintersasync

  //     // NOTE: treat this like pseudocode, I don't know Electron well
  //     const { webContents } = require('electron');
  //     const iframeWebContents = webContents.fromFrame(iframe)
  //     const printerList = await iframeWebContents.getPrintersAsync();
  //     const defaultPrinter = printerList.find(printer => printer.isDefault);
  //     iframeWebContents.print({ deviceName: defaultPrinter.name });
  // },
    // onAfterPrint: () => {
    //   console.log("after printing...")
    //   ,
    onPrintError:()=>console.log("error------"),
    removeAfterPrint: true,
  });

  const submitBarcode=()=>{
    const payload = {
          "quantity":codes[0].length,
          "serial_numbers": codes[0]
        }
        onSubmit(row?.id, payload)
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <Typography sx={{ mt: 2 }}>{row?.sap_id} / {row?.hsn_code} {">"} {row?.category} {">"} {row?.name}</Typography>
      <Divider />
      <Stack sx={{ flexDirection: 'row' }}>
        <Typography>Location</Typography>
      </Stack>
      <Stack sx={{ flexDirection: 'row' }}>
        <Stack sx={{ width: '40%' }}>
          <p className={styles.title} style={{ marginBottom: 0 }}>
            Unit : {row?.unit}
          </p>
          {row?.serialization_method === 2 && (
            <p className={styles.title} style={{ marginBottom: 0 }}>
              New Quantity :&nbsp;
              {row?.serialization_method === 2 && (
                scan.length
              )}
            </p>
          )}
        </Stack>
        <Stack sx={{ width: '60%' }}>
          <p className={styles.title} style={{ marginBottom: 0 }}>
            Present Quanitity : {row?.remaining_stock}
          </p>
          {row?.serialization_method === 2 && (
            <Stack sx={{ flexDirection: 'row', width: '100%', alignItems: 'center', gap: 1, mt: 1.5 }}>
              <label className={styles.title}  style={{ marginBottom: 0 }}>Scan Code :</label>
              <input ref={inputRef}  type="text" value={bar} onKeyUp={(e) => handleCode(e)} onChange={(e) => setBar(e.target.value)} />
            </Stack>
          )}
        </Stack>
      </Stack>
      {row?.serialization_method !== 2 && (
        <Stack sx={{ flexDirection: 'row', width: '100%', alignItems: 'center', gap: 1, mt: 1 }}>
        <label className={styles.title} >New Quanitity :</label>
        <input onChange={(e)=>setQuantity(e.target.value)} />
      </Stack>
      )}
      <SchemaExtractor
        data={data}
        errors={errors}
        onChange={handleChange}
        schema={SCHEMA}
        handleCounter={handleCounter}
      />

      {/* {step === 1 && (
        <JsonCreator
          setJson={(value) =>
            setData((prev) => ({ ...prev, specification: value }))
          }
          title="Specification"
        />
      )} */}
      {row?.serialization_method === 2 && (
        <Stack sx={{ mt: 2 }}>
          <Typography variant="h4" sx={{alignSelf:'center',letterSpacing:4}}>{bar}</Typography>
          {/* <Stack>
            <Grid display={'grid'} sx={{ pt: 1, }} gridTemplateColumns={{ sm: 'repeat(3, 1fr)', xs: 'repeat(3, 1fr)' }}>
              {scan.map((item,index) => (<Typography color={"primary"} sx={{backgroundColor:index % 2=== 0?'#D6F2FE':"#E7FFED",textAlign:'center'}} key={item}>{item}</Typography>))}
            </Grid>
          </Stack> */}
        </Stack>
      )}
      {row?.serialization_method === 3 && (
        <Stack sx={{ mt: 1 }}>
          <Button
            onClick={handlePrint}
            loading={loading}>
            Print Barcode
          </Button>
          {row?.serialization_method === 3 &&(
            codes&&codes.length&&(
                    <Stack sx={{overflow:'scroll',height:70,overflowX:"hidden"}}>
                      <Stack ref={printRef} gap={1} sx={{justifyItems:'center',alignItems:'center'}} >
                        {/* <Grid display={'grid'} sx={{ p: 1,px:2 }} gap={2} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}> */}
                        {codes&&codes[0].map((item,index)=>(<Barcode value={item} key={index} width={1} height={40} fontSize={16} margin={5}/>))}
                        {/* </Grid> */}
                      </Stack>
                    </Stack>
            )
          )}
        </Stack>
      )}
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          Cancel
        </Button>
        {row?.serialization_method === 3?(
          <Button onClick={() => submitBarcode()}
        
          loading={loading}>
          Save
        </Button>
        ):(
          <Button onClick={() => {
            const payload = {
              "quantity":row?.serialization_method === 1?quantity: scan.length,
              "serial_numbers": scan
            }
            onSubmit(row?.id, payload)
          }
          }
            loading={loading}>
            Save
          </Button>
        )}
       
        
      </Stack>
    </Modal>
  );
});

export default AddQuantity;
