import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";

const AddTest = ({ open, onClose, data, setData, onSubmit, loading ,tab,setSelectedQuestions,skills,users,testList}) => {

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  const [questions,setQuestions] = useState([])

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  
  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(TEST, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit()
      // setStep((prev) => prev + 1);
      return;
    }
  };

  const TEST = [
    {
      title: "Test Type",
      placeholder: "Select",
      type: SCHEMA_TYPES.SELECT,
      name: "tranning_type",
      validator: VALIDATORS.REQUIRED,
      keyValues:[{value:'Induction Training',key:"Induction Training"},{value:"Skill Test",key:"Skill Test"},{value:"Tranning Test",key:"Tranning Test"}]
    },
    data.tranning_type==='Skill Test' && (
      {
        title: "Skill",
        placeholder: "Select Skill",
        type: SCHEMA_TYPES.SELECT,
        name: "skill",
        validator: VALIDATORS.REQUIRED,
        keyValues:skills
      
      }
  ),
  data.tranning_type==='Skill Test' && ( {
    title: "Test Level",
    placeholder: "Select Test Level",
    type: SCHEMA_TYPES.SELECT,
    name: "level",
    validator: VALIDATORS.REQUIRED,
    keyValues:[{value:'Level 1',key:1},{value:"Level 2",key:2},{value:"Level 3",key:3}]
  
  }),
  {
    title: "Test Name",
    placeholder: "Select Test Level",
    type: SCHEMA_TYPES.INPUT,
    name: "testname",
    validator: VALIDATORS.REQUIRED,
  
  },
  {
    title: "Max Question",
    placeholder: "enter number of question",
    type: SCHEMA_TYPES.INPUT,
    inputType:'number',
    name: "totalQuestion",
    validator: VALIDATORS.REQUIRED,
  
  },
  data.tranning_type!=='Skill Test' && ( {
    title: "Passing Criteria",
    placeholder: "enter passing criteria",
    type: SCHEMA_TYPES.INPUT,
    name: "passing_criteria",
    inputType:'number',
    validator: VALIDATORS.REQUIRED,
  
  }),
  {
    title: "Time (in min)",
    placeholder: "enter time in min",
    type: SCHEMA_TYPES.INPUT,
    name: "time",
    inputType:'number',
    validator: VALIDATORS.REQUIRED,
  
  }
   ];

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">Create Test</h3>

      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={TEST}
      />
      
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          Next
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddTest;
