import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";
import { create } from "@mui/material/styles/createTransitions";

const useOutbound = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const selectedSearch = useSelector((state) => state?.SelectSearch?.data);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/?page=${page}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };
  const searchlistMat = async (page = 1,name,val) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/?search_field=${name}&search_value=${val}&page=${page}&page_size=${10}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };
  const searchOutbound = async (page = 1,name,val) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/${ENDPOINTS.OUTBOUNDLIST}/?search_field=${name}&search_value=${val}&page=${page}&page_size=${10}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  const listInbound = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/${ENDPOINTS.LIST_INBOUND}/?page=${page}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  const listIssued = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/${ENDPOINTS.ALL_ISSUED}/?page=${page}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };

  
  const fetchToolInbound = async (name,val) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TOOLS}/?serialization=1&search_field=${name}&search_value=${val}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [];
  };
  const fetchMaterialInbound = async (name,val) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/?serialization=1&search_field=${selectedSearch}&search_value=${name}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [];
  };
 
  const matOutBoundItem = async (item,internalId) => {
    const payload={
      "serial_number":item.barcode,
      "internal_id":internalId
    }
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.MATERIALS}/${item.id}/${ENDPOINTS.ALL_ISSUED}/`,
      payload,
      headers,
      // (res) => resolveResponse(res, null, "Tool created successfully!")
    );
    return res?.data;
  };
  const historyList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.MATERIALS}/${ENDPOINTS.OUTBOUNDLIST}/?page=${page}`,
      {},
      headers,
      Function.prototype
    );
    if (res?.data) {
      return [res?.data?.results, res?.data?.count];
    }
    return [[], 0];
  };
  const getBarInfo = async (code) => {
    const payload={
      "barcode":code
    }
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.MATERIALS}/${ENDPOINTS.SEARCH}/`,
      payload,
      headers,
      // (res) => resolveResponse(res, null, "Barcode Generated!")
    );
    if (res?.data) {
      return [res?.data];
    }
    return  0;
  };

  return {
    list,
    searchlistMat,
    matOutBoundItem,
    historyList,
    getBarInfo,
    searchOutbound,
    fetchToolInbound,
    fetchMaterialInbound
  };
};

export default useOutbound;
