import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useUser from "../../store/users";
import Button from "../../components/button";
import AddUser from "./AddUser";
import Input from "../../components/input";
import AddReport from "./AddReport"
import { toast } from "react-toastify";
import { IconButton, Stack, Tab, Tabs, Typography } from "@mui/material";
import { Delete } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import Category from "../../components/UI/Category";
import UploadCsv from "../store/UploadCsv";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import Tooltip from '@mui/material/Tooltip';
import moment from "moment/moment";
import PostAddIcon from '@mui/icons-material/PostAdd';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import AddAssign from "./AddAssign";
import AssignSkill from "./AssignSkill";
import useSkill from "../../store/skill";

const UserMainPage = () => {
  const [tab, setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [operators, setOperators] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [csvData, setCsvData] = useState([])
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [data, setData] = useState({
  });
  const [skills, setSkills] = useState([])
  const [userId, setUserId] = useState('')
  const [superVisor, setSuperVisor] = useState([])
  const [searchInput, setSearchInput] = useState('')
  const [op_id, setOp_id] = useState('')
  const [edit, setEdit] = useState(false)

  const { list, create, updateProfile, update, userProfile, remove, listByUserName, listByAadhar, updateOpSkill } = useUser();
  const { list: skill_list } = useSkill()
  useEffect(() => {
    setUsers([])
    fetchUsers();
  }, [page, tab]);

  useEffect(() => {
    fetchSkill();
  }, []);

  const fetchSkill = async () => {
    const [list] = await skill_list(page + 1);
    const newUsers = list.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setSkills(prevUsers => [...prevUsers, ...newUsers]);
  };

  const fetchUsers = async () => {
    setLoading("fetch");
    try {
      const [_list, _count] = await list(page + 1);
      const newOptr = _list.filter(item => item.is_operator);
      const newUsr = _list.filter(item => !item.is_operator);
      const superList = newUsr.map(item => ({
        value: item.username,
        key: item.id
      }));
      // console.log()
      setSuperVisor(superList)
      setUsers(newUsr);
      setTotal(_count);
      const operatorProfiles = await Promise.all(
        newOptr.map(item => userProfile(item.id))
      );

      setOperators(operatorProfiles);
    } catch (error) {
      console.error("Error fetching users:", error);
    } finally {
      setLoading(false);
    }
  };

  const fetchByName = async (name) => {
    setLoading("fetch")
    const [_list] = await listByUserName(name);
    if (_list !== undefined) {
      setUsers([_list]);
    }

    setLoading(false);
  }

  const fetchByAadhar = async (aadhar) => {
    const [_list, _count] = await listByAadhar(aadhar);
    if (_list !== undefined) {
      setOperators([_list])
    }
  }


  const handleAdd = async () => {
    const payload = { ...data }
    localStorage.setItem("op_ad_no", data.aadhar_no)
    payload.is_operator = tab === "Users" ? false : true
    if (tab === "Operators") {
      payload.username = `op_${data.aadhar_no}`
    }
    console.log("id response--------", payload)
    setLoading("fetch")
    const idRes = await create(payload)
    setUserId(idRes.id)
    setData({});
    // setModalOpen(false);
    // await fetchUsers();
    setLoading(false);
  };

  const handleAdd2 = async () => {
    const payload = { ...data }
    if (tab === "Operators") {
      payload.aadhar_number = localStorage.getItem("op_ad_no")
    }
    payload.is_operator = tab === "Users" ? false : true
    setLoading("fetch")
    let profileId = await userProfile(userId)
    // console.log("id response---profileId-----",profileId)
    await updateProfile(profileId.id, payload)
    // setData({});
    setModalOpen(false);
    await fetchUsers();
    setLoading(false);
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // tab === "Users" ? await remove(id): await removeOperator(id);
    tab === "Users" ? await remove(id) : await remove(id);
    await fetchUsers();
    setLoading(false);
  };

  const handleUpdate = async (id, is_admin, type) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    const payload2 = {
      "status": is_admin === true ? false : true
    }
    // tab === "Users" ? await update(id,type==="status"?payload2:payload):updateOperator(id,type==="status"?payload2:payload)
    tab === "Users" ? await update(id, type === "status" ? payload2 : payload) : update(id, type === "status" ? payload2 : payload)
    await fetchUsers();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Email",
      render: (rowData) => rowData.email,
    },
    {
      id: 2,
      title: "User Name",
      render: (rowData) => rowData.username,
    },
    {
      id: 3,
      title: "Full Name",
      render: (rowData) => rowData.fullname,
    },
    {
      id: 4,
      title: "Employee Id",
      render: (rowData) => rowData.employee_id,
    },
    {
      id: 5,
      title: "Role",
      render: (rowData) => <Category title={rowData.is_admin ? "admin" : "Na"} />,
    },
    {
      id: 6,
      title: "Actions",
      render: (rowData) => (<>
        <Stack flexDirection={'row'} sx={{ gap: 1 }}>
          <Button width={"10%"} color={rowData.status ? "green" : "red"}
            onClick={() => handleUpdate(rowData?.id, rowData?.status, "status")}
          >
            {rowData.status ? "Active" : "Inactive"}
          </Button>
          <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => handleDelete(rowData?.id)}
          >
            <Delete color="error" fontSize="small" />
          </IconButton>
          {tab === "Users" && rowData?.is_admin === false ? (
            <Tooltip title="Make Admin">
              <IconButton
                sx={{ height: 25, width: 25 }}
                onClick={() => handleUpdate(rowData?.id, rowData?.is_admin, "delete")}
              >
                <AdminPanelSettingsIcon color={rowData.is_admin ? "error" : "success"} fontSize="small" />
              </IconButton>
            </Tooltip>
          ) : null}
        </Stack>
      </>
      ),
    },
  ];
  const OP = [
    {
      id: 4,
      title: "Aadhar Number",
      render: (rowData) => rowData.aadhar_number,
    },
    // {
    //   id: 3,
    //   title: "Full Name",
    //   render: (rowData) => rowData.fullname,
    // },
    {
      id: 1,
      title: "Date of Joining",
      render: (rowData) => moment(rowData.date_of_joining).format('ll'),
    },
    {
      id: 11,
      title: "Supervisor",
      render: (rowData) =>
        <Stack sx={{ flexDirection: 'row', alignItems: 'center' }}>
         {rowData.passed_induction &&(
           <Typography>
            {rowData.supervisor?.username}
          </Typography>
         )}
          {tab !== "Users" && (
            rowData.passed_induction?(
            <Tooltip title={rowData.supervisor ? "Edit Supervisor" : "Add Supervisor"}>
              <IconButton
                sx={{ height: 25, width: 25 }}
                onClick={() => { rowData.supervisor ? setEdit(true) : setEdit(false); setUserId(rowData.id); setOp_id(rowData.user.id); setModalOpen("addSupervisor") }}
              >
                {rowData.supervisor ? <EditIcon fontSize="small" color="primary" /> : <AddCircleIcon />}
              </IconButton>
            </Tooltip>
            ):(
              <Typography>Induction not passed</Typography>
            )
          )}
        </Stack>
    },
    // {
    //   id: 12,
    //   title: "Skill",
    //   render: (rowData) => 
    //   <>
    //   {tab!=="Users" &&(
    //     <Tooltip title="Add Skill">
    //     <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() =>{setUserId(rowData.user.id); setModalOpen("addSkill")}}
    //     >
    //     <AddCircleIcon  />
    //     </IconButton>
    //     </Tooltip>
    //     )}
    //     </>
    // },
    {
      id: 6,
      title: "Actions",
      render: (rowData) => (<>
        <Stack flexDirection={'row'} sx={{ gap: 1, alignItems: 'center' }}>
          <Button width={"10%"} color={rowData.user.status ? "green" : "red"}
            onClick={() => handleUpdate(rowData?.user.id, rowData?.user.status, "status")}
          >
            {rowData.user.status === true ? "Active" : "Inactive"}
          </Button>
          {/* <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton> */}
          {tab === "Users" && rowData?.is_admin === false ? (
            <Tooltip title="Make Admin">
              <IconButton
                sx={{ height: 25, width: 25 }}
                onClick={() => handleUpdate(rowData?.id, rowData?.is_admin, "delete")}
              >
                <AdminPanelSettingsIcon color={rowData.is_admin ? "error" : "success"} />
              </IconButton>
            </Tooltip>
          ) : null}
          {/* {tab!=="Users" ?(
        <Tooltip title="Add Daily Report">
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => setModalOpen("Report")}
        >
        <PostAddIcon  />
        </IconButton>
        </Tooltip>
        ):null} */}
        </Stack>
      </>
      ),
    },
  ];

  const handleSubmitCSV = async () => {
    csvData.slice(1).map(async (item) => {
      if (tab === "Users") {
        if (item[0] !== '') {
          const payload = {
            "username": item[0],
            "fullname": item[1],
            "email": item[2]
          }
          const res = await create(payload)
          const id = res?.id
          const payload2 = {
            "phone_number": item[3],
            "aadhar_number": item[4],
            "employee_id": item[5],
            "employee_type": item[6],
            "date_of_joining": item[7],
            // "supervisor":item[8]
          }
          let profileId = await userProfile(id)
          await updateProfile(profileId.id, payload2)
        } else { return }
      } else {
        if (item[4] !== '' && item[0] !== '') {
          const payload = {
            "aadhar_number": item[4],
            "username": item[0],
            "fullname": item[1],
            "is_operator": true,
          }
          const res = await create(payload)
          const id = res?.id
          const payload2 = {
            "is_operator": true,
            "phone_number": item[3],
            "employee_id": item[5],
            "employee_type": item[6],
            "date_of_joining": item[7],
            // "supervisor":item[8]
          }
          let profileId = await userProfile(id)
          await updateProfile(profileId.id, payload2)
        } else { return }
      }
    })
    setModalOpen(false);
    await fetchUsers()
  }

  const handleSupervisor = async () => {
    setLoading("fetch");
    const payload = {
      "add_supervisor": data.supervisor
    }
    updateProfile(userId, payload)
    handleSkill()
    setModalOpen(false);
    // await fetchUsers();
    setLoading(false);

  }

  const handleSkill = async () => {
    setLoading("fetch");
    const payload = {
      "skill": data.skill,
      "supervisor": data.supervisor
    }
    updateOpSkill(op_id, payload)
    setModalOpen(false);
    await fetchUsers();
    setLoading(false);

  }

  return (
    <div className="mx-3 my-2 fw-500">
      <Tabs value={tab} onChange={(_e, v) => { setTab(v); setPage(0); setSearchInput('') }} className="mb-2 fw-500">
        {["Users", "Operators"].map((label, i) => (
          <Tab label={label} className="fw-500" value={label} />
        ))}
      </Tabs>
      <div className="flex items-center justify-between">
        <Stack>
          <Input
            height="37px"
            value={searchInput}
            placeholder={tab === "Users" ? "Search By username" : 'Search by Aadhar Number'}
            onChange={(e) => {
              setSearchInput(e.target.value);
              if (tab === "Users") { fetchByName(e.target.value) } else { fetchByAadhar(e.target.value) }
            }}
          />
        </Stack>
        {/* <Typography className="mt-2 fw-500">
          {tab}<span className="fw-500 ml-1 ">({users.length})</span>
        </Typography> */}
        <Stack justifyContent="flex-end" direction="row" sx={{ mb: 1 }}>
          <Button onClick={() => setModalOpen("csvUser")} bordered classes="mr-1">
            Upload CSV
          </Button>
          <Button onClick={() => setModalOpen("add")}>Create {tab}</Button>
        </Stack>
      </div>
      <Table
        columns={tab === "Users" ? COLUMNS : OP}
        rows={tab === "Users" ? users : operators}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddUser
        open={modalOpen === "add"}
        onClose={() => setModalOpen(false)}
        data={data}
        setData={setData}
        onSubmit={handleAdd}
        loading={loading === "add"}
        tab={tab}
        handleAdd2={handleAdd2}
      />
      <AddReport
        open={modalOpen === "Report"}
        onClose={() => setModalOpen(false)}
        onSubmit={handleAdd}
      />
      <AddAssign
        data={data}
        setData={setData}
        open={modalOpen === "addSupervisor"}
        onClose={() => setModalOpen(false)}
        onSubmit={handleSupervisor}
        users={superVisor}
        skill={skills}
        edit={edit}
      />
      <AssignSkill
        data={data}
        setData={setData}
        open={modalOpen === "addSkill"}
        onClose={() => setModalOpen(false)}
        onSubmit={handleSkill}
        users={skills}
      />
      <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csvUser"}
        onSubmit={handleSubmitCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
        tab={tab}
      />
    </div>
  );
};

export default UserMainPage;
