import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

const AddStation = ({ open, onClose, data, setData, onSubmit, loading,edit  }) => {
  const USER_SCHEMA = [
    {
      title: "Alias",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "alias",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Model",
      placeholder: "Enter model",
      type: SCHEMA_TYPES.INPUT,
      name: "model",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Machine ID",
      placeholder: "Enter machine ID",
      type: SCHEMA_TYPES.INPUT,
      name: "machineID",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Serial Number",
      placeholder: "Enter serial number",
      type: SCHEMA_TYPES.INPUT,
      name: "snumber",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Supplier",
      placeholder: "Enter supplier",
      type: SCHEMA_TYPES.INPUT,
      name: "supplier",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Purchase Date",
      type: SCHEMA_TYPES.DATE,
      name: "purchase_date",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Warranty Expiration",
      type: SCHEMA_TYPES.DATE,
      name: "warranty",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "API Endpoint",
      placeholder: "Enter API Endpoint",
      type: SCHEMA_TYPES.INPUT,
      name: "api_endpoint",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Ideal Cycle Time (seconds)",
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: "api_endpoint",
      validator: VALIDATORS.REQUIRED,
    },
   
  ];
  const [errors, setErrors] = useState(new Map());
  const [isEndLine,setIsEndLine] = useState('Yes')
  const [PerformanceCal,setPerformanceCal] = useState('Job/Part based')
  const [cycleTime, setCycleTime] = useState('Yes')

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">{edit===true?"EDIT":"ADD"} Station</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
      />
      <Stack mt={1}>
      <FormControl>
      <FormLabel sx={{color:'black'}} id="demo-row-radio-buttons-group-label">Is end of line</FormLabel>
      <RadioGroup
        row
        aria-labelledby="demo-row-radio-buttons-group-label"
        name="row-radio-buttons-group"
        value={isEndLine}
        onChange={(e)=>setIsEndLine(e.target.value)}
      >
        <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
        <FormControlLabel value="No" control={<Radio />} label="No" />
      </RadioGroup>
    </FormControl>
      <FormControl>
      <FormLabel sx={{color:'black'}} id="demo-row-radio-buttons-group-label">Performance Calculation</FormLabel>
      <RadioGroup row aria-labelledby="demo-row-radio-buttons-group-label" name="row-radio-buttons-group"
      value={PerformanceCal}
      onChange={(e)=>setPerformanceCal(e.target.value)}>
        <FormControlLabel value="Job/Part based" control={<Radio />} label="Job/Part based" />
        <FormControlLabel value="Output based" control={<Radio />} label="Output based" />
      </RadioGroup>
    </FormControl>
      <FormControl>
      <FormLabel sx={{color:'black'}} id="demo-row-radio-buttons-group-label">Fixed Cycle Time</FormLabel>
      <RadioGroup row aria-labelledby="demo-row-radio-buttons-group-label" name="row-radio-buttons-group"
      value={cycleTime}
      onChange={(e)=>setCycleTime(e.target.value)}>
        <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
        <FormControlLabel value="No" control={<Radio />} label="No" />
      </RadioGroup>
    </FormControl>
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddStation;
